/*
*   Name: Bizagi Panel Container Class
*   Author: Diego Parra
*   Comments:
*   -   This script will define a panel container class for all devices
*/

$.Class.extend("bizagi.rendering.panel", {}, {

  /*
                                               *   Constructor
                                               */
  initializeData: function initializeData(params) {
    var self = this;

    // Call base
    self._super(params);

    // Override properties
    var properties = self.properties;
    properties.displayName = properties.displayName || "";

    if (properties.type == "nestedForm") {
      var formContainer = self.getFormContainer();
      formContainer.rendersById[properties.idForm] = self;
    }
  },


  /*
     *   Render the container layout
     */
  renderContainer: function renderContainer() {
    var self = this;
    var properties = this.properties;
    var template = self.renderFactory.getTemplate("panel");

    // Render the panel
    var html = $.fasttmpl(template, {
      displayName: properties.displayName,
      loading: properties.loading,
      message: properties.message,
      orientation: properties.orientation,
      uniqueId: properties.uniqueId,
      cssClass: properties.cssclass });


    // Render children
    html = self.replaceChildrenHtml(html, self.renderChildrenHtml());
    return html;
  },

  /*
     *   Process the html rendering object
     */
  postRenderContainer: function postRenderContainer(panel) {
    var self = this;
    var properties = self.properties;

    // Set height
    if (properties.height) {
      panel.height(properties.height);
    }

    // Call base
    self._super(panel);
  } });