/**
 *   Name: Bizagi Render Hidden Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for hiddens
 */

$.Class.extend("bizagi.rendering.hidden", {}, {

  /**
                                                 * Returns the in-memory processed element
                                                 */
  renderControl: function renderControl() {
    if (this.getMode() === "design") {
      return "<div>" + bizagi.localization.getResource("render-hidden-displayName") + "</div>";
    } else {
      return "<div />";
    }
  },

  /**
      * Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    var properties = this.properties;

    // Call base
    this._super();

    // Set the initial value
    if (properties.value !== undefined) {
      this.setDisplayValue(properties.value);
    }
  } });