/*
*   Name: Bizagi Render List Class
*   Author: Diego Parra
*   Comments:
*   -   This script will define basic stuff for list renders
*/

$.Class.extend("bizagi.rendering.list", {}, {

  /**
                                               * Update or init the element data
                                               * @param {} data 
                                               * @returns {} 
                                               */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Fill default properties
    var properties = this.properties;
    properties.listSize = properties.listSize || 0;
  },

  /**
      * Gets the template used by the combo render can be overriden in subclasses
      * to reuse logic and just change the template
      * @returns {} 
      */
  getTemplateName: function getTemplateName() {
    return "list";
  },

  /**
      * Determines if we need to show the current data regardless if it belongs
      * to data or not Can be overriden to change behaviour    
      * @returns {} 
      */
  showCurrentData: function showCurrentData() {
    return false;
  } });