/**
 *   Name: Bizagi Render Simple Link Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for link renders
 */

$.Class.extend("bizagi.rendering.link", {}, {

  /**
                                               * Update or init the element data
                                               * @param {} data
                                               * @returns {}
                                               */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Set properties
    var properties = this.properties;
    properties.required = false;
    properties.displayType = "value";

    // Calculate layout properties
    this.calculateInitialLayoutProperties();
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {}
      */
  renderControl: function renderControl() {
    var self = this;
    var template = self.renderFactory.getTemplate("link");

    // Render template
    return $.fasttmpl(template, {
      displayName: self.properties.displayName,
      align: self.properties.valueAlign,
      editable: self.properties.editable });

  },

  /**
      * Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    this._super();
    this.getLabel().addClass("bz-form-label--hidden");
  },

  /**
      * Method to render non editable values
      * @returns {*}
      */
  renderReadOnly: function renderReadOnly() {
    // Executes the same template than normal render
    return this.renderControl();
  },

  /**
      * Template method to implement in each device to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this._super();
    this.getLabel().addClass("bz-form-label--hidden");
  },

  /**
      * Public method to determine if a value is valid or not
      * @param invalidElements
      */
  isValid: function isValid(invalidElements) {
    // Automatically this render is valid, so we can't check a required link
  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    // Do nothing
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @returns {boolean}
      */
  canBeSent: function canBeSent() {
    return false;
  } });