$.Class.extend("bizagi.rendering.handSignature", {}, {

  BASE_IMAGE: "data:image/png;base64,",
  EMPTY_IMAGE: "R0lGODlhAQABAIAAAP7//wAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw==",
  EMPTY_VALUE: "",
  DELAY_TO_SAVE: 1000,

  // Fallback if resources
  RESOURCES_FALLBACK: {
    DONE_BUTTON: "Done",
    EDIT_BUTTON: "Edit",
    CLEAR_BUTTON: "Clear",
    UPLOAD_SUCCESS: "Your signature have been uploaded successfully",
    UPLOAD_ERROR: "The signature have not been uploaded. Try again",
    TITLE_DONE: "Click \"Done\" to upload the signature",
    TITLE_DRAW: "Draw here your signature",
    TITLE_EDIT: "Click \"Edit\" to start drawing" },


  // Type of state
  STATE_TYPE: {
    edit: 1,
    draw: 2,
    done: 3,
    success: 4,
    error: 5 },


  /**
                 * Whether or not is plugin initialized
                 * @var isOfflineForm boolean
                 */
  pluginInitialized: false,

  saveDeferred: null,
  saveTimestamp: 0,

  /**
                     * Update or init the element data
                     * @param data
                     */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Fill default properties
    var properties = this.properties;

    properties.editable = bizagi.util.parseBoolean(properties.editable) === true;
    properties.addUrl = properties.addUrl || this.dataService.serviceLocator.getUrl("form-submit-data-upload");
    properties.deleteUrl = properties.deleteUrl || this.dataService.serviceLocator.getUrl("render-upload-delete-url");
    properties.isChanged = false;

    this.currentState = this.STATE_TYPE.edit;
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {*|jQuery}
      */
  renderControl: function renderControl() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("hand-signature");

    return $.fasttmpl(template, {
      id: properties.id,
      xpath: properties.xpath,
      editable: properties.editable,
      isDesign: !self.isExecutionMode(),
      render_signature_action_edit: self.getResourceByKey("render-signature-action-edit", self.RESOURCES_FALLBACK.EDIT_BUTTON),
      render_signature_action_done: self.getResourceByKey("render-signature-action-done", self.RESOURCES_FALLBACK.DONE_BUTTON),
      render_signature_action_clear: self.getResourceByKey("render-signature-action-clear", self.RESOURCES_FALLBACK.CLEAR_BUTTON) });

  },

  /**
      * After rendering element
      */
  postRender: function postRender() {
    var self = this;

    // Apply plugin
    if (this.isExecutionMode()) {
      var $container = self.getSignatureContainer();

      // Wait until current element is rendered
      self.setResizeObserver($container, function (initialized) {
        self.renderSignature();
        self.setState(self.currentState);

        if ([self.STATE_TYPE.draw, self.STATE_TYPE.done].includes(self.currentState)) {
          $container.BzjSignature("enable");
        }
      });
    }
  },

  /**
      * Render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;

    // Apply plugin
    if (this.isExecutionMode()) {
      var $container = self.getSignatureContainer();

      // Wait until current element is rendered
      self.setResizeObserver($container, function () {
        self.renderSignature();
      });
    }
  },

  /**
      * Method to render non editable values
      */
  renderReadOnly: function renderReadOnly() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("hand-signature-readonly");

    return $.fasttmpl(template, {
      id: properties.id,
      xpath: properties.xpath,
      editable: properties.editable,
      isDesign: !self.isExecutionMode() });

  },

  setData: function setData(value) {
    var self = this;
    var previousValue = self.getData();

    if (bizagi.util.isEmpty(value)) {
      this.properties.binaryValue = null;
    } else {
      if (value instanceof Blob) {
        this.properties.binaryValue = value;
      } else {
        this.properties.binaryValue = bizagi.util.b64toBlob(value, "image/png");
      }
    }

    if (previousValue != this.properties.binaryValue) {

      if (self.properties.required && self.isExecutionMode()) {
        var isValidField = !self.checkRequired([]);
        self.changeRequiredLabel(isValidField);
      }

      if (this.properties.isChanged) {
        self.triggerRenderChange();
        self.triggerRenderValidate();
      }
    }
  },

  getData: function getData() {
    return this.properties.binaryValue || [];
  },

  getBase64: function getBase64() {
    return this.properties.base64 || "";
  },

  /**
      * Returns the internal value
      * @returns {*|Array}
      */
  getValue: function getValue() {
    return this.properties.value || [];
  },

  /**
      * Check if a render has no value for required validation
      */
  hasValue: function hasValue() {
    if (!this.pluginInitialized) {
      return !bizagi.util.isEmpty(this.properties.value);
    } else {
      var value = this.getData();
      return !bizagi.util.isEmpty(value);
    }
  },

  /**
      * Check if control is valid or not
      * @param invalidElements
      * @returns {boolean}
      */
  isValid: function isValid(invalidElements) {
    var self = this;

    // Don't check non-visible renders
    if (bizagi.util.parseBoolean(self.properties.visible) === false) {
      return true;
    }

    // Don't check non-editable renders
    if (bizagi.util.parseBoolean(self.properties.editable) === false) {
      return true;
    }

    // Clear error message
    self.setValidationMessage("");

    // Check required
    if (self.properties.required && !self.hasValue()) {
      var message = self.getResource("render-required-text").replaceAll("#label#", self.properties.displayName);
      invalidElements.push({ xpath: self.properties.xpath, message: message });
      return false;
    }

    return true;
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @returns {boolean}
      */
  canBeSent: function canBeSent() {
    var form = this.getFormContainer();
    if (!form.isOfflineForm) return true;
    return this.properties.isChanged;
  },

  /**
      * Process an upload file into the server
      * @param {} dataFile
      * @returns {}
      */
  processUploadFile: function processUploadFile(dataFile) {
    var data = {
      url: this.properties.addUrl,
      formData: this.buildAddFileParams(dataFile) };


    return this.dataService.processUploadFile(data);
  },

  /**
      * Build add params to send to the server
      * @param {} dataFile
      * @returns {}
      */
  buildAddFileParams: function buildAddFileParams(dataFile) {
    var self = this;
    var properties = self.properties;
    var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;

    // Get form data for POSTing
    var formData = new FormData();
    var form = self.getFormContainer();

    // Build add params to send to the server
    formData.append(prefix + "xpath", properties.xpath);
    formData.append(prefix + "idRender", properties.id);
    formData.append(prefix + "xpathContext", properties.xpathContext);
    formData.append(prefix + self.Class.BA_PAGE_CACHE, properties.idPageCache);
    formData.append(self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId", form.properties.sessionId);
    formData.append(prefix + "action", "savefile");

    var fileName = "signature_" + bizagi.util.randomNumber() + ".png";
    formData.append(self.properties.xpath, dataFile, fileName);

    if (properties.contexttype) {
      formData.append(prefix + "contexttype", properties.contexttype);
    }

    try {
      if (typeof BIZAGI_SESSION_NAME !== "undefined") {
        formData.append(BIZAGI_SESSION_NAME, form.properties.sessionId);
      } else {
        formData.append("JSESSIONID", form.properties.sessionId);
      }
    } catch (e) {
      formData.append("JSESSIONID", form.properties.sessionId);
    }

    return formData;
  },

  /**
      * Builds the upload item url
      * @param params
      * @returns {*}
      */
  buildItemUrl: function buildItemUrl() {
    var self = this;
    var form = self.getFormContainer();
    var properties = self.properties;
    var isVersion2OrSuperior = form.properties.compatibilityVersion >= 2;
    var url = "";

    if (!this.isExecutionMode()) return $.Deferred().resolve(url).promise();

    var parameters = {
      url: properties.dataUrl,
      xpath: properties.xpath,
      idRender: properties.id,
      xpathContext: properties.xpathContext,
      idPageCache: properties.idPageCache,
      property: "fileFullContent",
      queue: "onlineimages",
      sessionId: form.properties.sessionId,
      contexttype: properties.contexttype || undefined,
      dataType: "text" };


    if (isVersion2OrSuperior) {

      if (bizagi.util.isEmpty(self.properties.value) && !self.hasValue()) return $.Deferred().resolve("").promise();

      parameters.property = "fileBinaryContent";
      parameters.sessionId = form.properties.sessionId;

      return self.dataService.downloadFile(parameters).
      then(function (blob) {
        self.setData(blob);
        if (blob == null || window.Blob && blob instanceof Blob && blob.size === 0) return null;
        return bizagi.util.blobTob64(blob, false);
      }).
      then(function (base64) {
        return bizagi.util.getDataUrl(base64, properties.url);
      });
    }

    return self.dataService.multiaction().getPropertyData(parameters, "text").
    then(function (data) {
      if (data.charAt(0) === "\"") data = data.slice(1, -1);
      if (data !== "") url = self.BASE_IMAGE + data;
      self.setData(bizagi.util.b64toBlob(data));
      return url;
    });
  },

  /**
      * Call the form failhandler with an specific error
      * @param {} error
      * @returns {}
      */
  executeFormFailHandler: function executeFormFailHandler(error) {
    var error_obj = bizagi.util.getErrorFormatted(error);
    var message = error_obj;

    if (typeof error_obj === "undefined" || error_obj && !error_obj.message) {
      message = Object.assign({}, _typeof(error_obj) === "object" ? error_obj : {}, {
        "message": bizagi.localization.getResource("workportal-general-error") });

    }

    this.getFormContainer().failHandler(message);
  },

  /**
      * Save data control
      * @returns {*}
      */
  saveSignature: function saveSignature(dataImage) {
    var self = this;

    var defer = new $.Deferred();
    var form = self.getFormContainer();


    form.useOrSetLoaderDeferred();
    form.appendWaitForLoader({ promise: defer.promise() });

    return $.when(self.postSignature(dataImage)).
    then(function (base64) {
      self.setData(base64);
      self.properties.url = bizagi.util.getDataUrl(base64, self.EMPTY_VALUE);
      self.executePartialSaveForm();
      defer.resolve();
    }).fail(function (error) {
      self.executeFormFailHandler(error);
      defer.reject();
    });
  },

  executePartialSaveForm: function executePartialSaveForm() {
    var self = this;
    var data = {};
    var form = this.getFormContainer();

    if (!form.isOfflineForm) return;
    self.collectDataToSave(data);
    form.partialSubmitFormOffline(data).then(function () {
      //console.log("Signature saved");
    });
  },

  /**
      * Delete image
      * @returns {*}
      */
  deleteImage: function deleteImage() {
    return this.dataService.deleteImage({
      url: this.properties.deleteUrl,
      idRender: this.properties.id,
      xpath: this.properties.xpath,
      xpathContext: this.properties.xpathContext,
      idPageCache: this.properties.idPageCache,
      contexttype: this.properties.contexttype });

  },

  /**
      * Execute upload image
      * @param dataImage
      * @returns {*}
      */
  postSignature: function postSignature(dataImage) {
    var signature = dataImage.replace(/^data:image\/(png|jpg|jpeg);base64,/, "");
    return this.postSignatureOnline(signature);
  },

  /**
      * Execute upload image for online mode
      * @param signature
      * @returns {*}
      */
  postSignatureOnline: function postSignatureOnline(signature) {
    var self = this;
    var dataFile = self.getDataFileFromSignature(signature);

    if (bizagi.util.isEmpty(dataFile)) {
      return self.getData();
    }

    return self.processUploadFile(dataFile).then(function () {
      self.properties.isChanged = true;
      return signature;
    });
  },

  /**
      * Clean html because some scenarios render twice
      */
  emptyControlByAsyncRenderDeferred: function emptyControlByAsyncRenderDeferred() {
    var $control = this.getControl();
    $control.empty();
  },

  // region // #region METHODS TO OFFLINE

  /**
   * Create an image offline
   * @param path File in Browser or Path in Mobile Devices
   * @param promise Promise to be resolved when save is completed
   */
  saveImageOffline: function saveImageOffline(path) {
    var self = this;
    var id = self.buildOfflineImageId();

    return $.when(function () {
      return path;
    }()).then(function (blobImage) {
      // Save the image as attachment
      var formParams = self.getFormParams();
      var parameters = {
        idCase: formParams.idCase,
        idWorkitem: formParams.idWorkitem,
        id: id,
        operationType: "edition",
        offlineType: formParams.offlineType };


      return self.dataService.saveOfflineImage(parameters, blobImage);
    });
  },

  /**
      * Add the render data to the given collection in order to send data to the server
      * @param {} renderValues
      * @returns {}
      */
  collectDataToSave: function collectDataToSave(renderValues) {
    var self = this;
    if (self.isOfflineForm && bizagi.util.isOfflineFormsV2Supported()) {
      if (!self.properties.isChanged) return;

      renderValues[self.properties.xpath] = {
        offlineId: self.buildOfflineImageId(),
        name: self.dataService.fileService.getOfflineImageName(self.getValue()),
        isSignature: true };

    }
  },

  /**
      * This method allows wait until the signature is persisted to execute the form save 
      * @param {*} renderValues 
      * @returns 
      */
  collectData: function collectData(renderValues) {
    var self = this;
    if (self.saveTimestamp == 0) return;
    var tmpTimeStamp = new Date().getTime() - self.saveTimestamp;

    if (tmpTimeStamp < self.DELAY_TO_SAVE) {
      var def = new $.Deferred();
      setTimeout(function () {
        $.when(self.saveDeferred).always(function () {
          def.resolve();
        });
      }, self.DELAY_TO_SAVE + 10 - tmpTimeStamp);
      renderValues.deferreds = renderValues.deferreds || [];
      renderValues.deferreds.push(def.promise());
    } else
    if (self.saveDeferred) {
      renderValues.deferreds = renderValues.deferreds || [];
      renderValues.deferreds.push(self.saveDeferred.promise());
    }
  },

  /**
      * Builds the upload item url
      * @returns {*}
      */
  buildItemUrlOffline: function buildItemUrlOffline() {
    var self = this;
    var properties = self.properties;
    var formParams = self.getFormParams();
    var imageId = self.buildOfflineImageId();
    var loadImageFromForm = !properties.isChanged;

    var parameters = {
      idCase: formParams.idCase,
      idWorkitem: formParams.idWorkitem,
      id: imageId,
      offlineType: formParams.offlineType,
      loadImageFromForm: loadImageFromForm };


    return $.when(self.dataService.getOfflineImage(parameters, bizagi.util.db.AttachmentType.Blob)).
    then(function (blob) {
      self.setData(blob);
      if (blob == null || window.Blob && blob instanceof Blob && blob.size === 0) return null;
      return bizagi.util.blobTob64(blob, false);
    }).
    then(function (base64) {
      return bizagi.util.getDataUrl(base64, properties.url);
    });
  },

  // endregion // #endregion

  // region // #region UTILS TO CONTROL

  /**
   * Update actions
   * @param state
   */
  setState: function setState(state) {
    var self = this;
    var $actions = $(".bz-actions", this.getControl());
    var message = "";

    self.currentState = state;

    switch (state) {
      case self.STATE_TYPE.edit:
        message = self.getResourceByKey("render-signature-title-edit", self.RESOURCES_FALLBACK.TITLE_EDIT);
        break;
      case self.STATE_TYPE.draw:
        message = self.getResourceByKey("render-signature-title-draw", self.RESOURCES_FALLBACK.TITLE_DRAW);
        break;
      case self.STATE_TYPE.done:
        message = self.getResourceByKey("render-signature-title-done", self.RESOURCES_FALLBACK.TITLE_DONE);
        break;}


    // Update message
    $(".bz-actions__status", $actions).text(message);

    // Update actions
    self.setActions(state);
  },

  /**
      * Update action state
      * @param state
      */
  setActions: function setActions(state) {
    var self = this;
    var $actions = $(".bz-actions", this.getControl());

    if ([self.STATE_TYPE.draw].includes(state)) {
      // Hidden action
      $actions.find("[data-action='edit']").addClass("bz-hidden");

      // Show actions
      $actions.find("[data-action='clear']").removeClass("bz-hidden");
      $actions.find("[data-action='save']").removeClass("bz-hidden");
    } else if ([self.STATE_TYPE.edit].includes(state)) {
      // Show actions
      $actions.find("[data-action='edit']").removeClass("bz-hidden");

      // Hidden action
      $actions.find("[data-action='clear']").addClass("bz-hidden");
      $actions.find("[data-action='save']").addClass("bz-hidden");
    }
  },

  buildOfflineImageId: function buildOfflineImageId() {
    var self = this;
    var form = self.getFormContainer();
    var isVersion2OrSuperior = form.properties.compatibilityVersion >= 2;
    var isStartForm = !form.properties.isTaskForm;

    return self.dataService.fileService.buildOfflineImageId(self.properties.value, self.properties.xpathContext, self.properties.xpath, isVersion2OrSuperior, isStartForm);
  },

  buildOfflineFileLoadingKey: function buildOfflineFileLoadingKey() {
    var self = this;
    var form = self.getFormContainer();
    var idCase = form.getParams().idCase;
    var idWorkitem = form.getParams().idWorkitem;

    var key = idCase;
    if (!bizagi.util.isEmpty(idWorkitem)) key += "-" + idWorkitem;
    key += "-";
    if (!bizagi.util.isEmpty(self.properties.xpathContext)) key += "-" + self.properties.xpathContext;
    key += self.properties.xpath;

    return key;
  },

  getDataFileFromSignature: function getDataFileFromSignature(signature) {
    try {
      var binary = atob(signature);
      var array = bizagi.util.stringToByteArray(binary);

      return new Blob([new Uint8Array(array)], { type: "image/png" });
    } catch (e) {
      return "";
    }
  },

  setSignature: function setSignature($container) {
    var url = this.properties && this.properties.url;

    try {
      if (!bizagi.util.isEmpty(url)) {
        $container.BzjSignature("reset");
        $container.BzjSignature("setData", url);
      }
    } catch (err) {
    }
  },

  /**
      * Provides a way to asynchronously observe changes in the resize
      * of a target element
      * @param $context
      * @param callbackFn
      */
  setResizeObserver: function setResizeObserver($context, callbackFn) {
    var self = this;
    var initialWidth = 0;

    var resizeObserver = new ResizeObserver(function (entries) {
      var entry = entries[0];
      if (entry.contentRect) {
        if (initialWidth !== entry.contentRect.width && entry.contentRect.width !== 0) {
          initialWidth = entry.contentRect.width;
          if (typeof callbackFn === "function") {
            callbackFn();
          }
        }
      }
    });

    resizeObserver.observe($context.get(0));
  },

  /**
      * Get container of control
      * @param selectorClass
      * @returns {*}
      */
  getSignatureContainer: function getSignatureContainer(selectorClass) {
    var contextClass = selectorClass || ".bz-signature__container";
    return this.getSignatureContext(contextClass);
  },

  /**
      * Get context of control
      * @param selectorClass
      * @returns {jQuery|HTMLElement}
      */
  getSignatureContext: function getSignatureContext(selectorClass) {
    var $control = this.getControl();
    return $(selectorClass, $control);
  },

  onSignatureClear: function onSignatureClear() {
    //To implement in the specific class
  },

  cleanData: function cleanData() {
    var form = this.getFormContainer();

    var result = this.onSignatureClear(this.getSignatureContainer());

    form.useOrSetLoaderDeferred();
    form.appendWaitForLoader({ promise: result.promise() });

    return result;
  }
  // endregion // #endregion
});