/**
 *   Name: Bizagi Render Readonly Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for readonly renders
 */

$.Class.extend("bizagi.rendering.readonly", {}, {

  /**
                                                   * Update or init the element data
                                                   * @param data
                                                   */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {*}
      */
  renderControl: function renderControl() {
    var template = this.renderFactory.getTemplate("readonly-value");
    var value = this.getMode() == 'execution' ?
    this.getFormattedValue(this.properties.value, this.properties.dataType) :
    this.getDesignModeValue(this.properties.renderType);

    // Render template
    var html = $.fasttmpl(template, {
      id: this.properties.id,
      value: value });


    return html;
  },

  /**
      * Method to render non editable values
      * @returns {*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    return this.renderControl();
  },

  /**
      *  Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    // DON'T DO ANYTHING
  },

  /**
      * Sets the internal value
      * @param value
      * @param triggerEvents
      */
  setValue: function setValue(value, triggerEvents) {
    // DON'T DO ANYTHING
  },

  /**
      * Get data value with format
      * @param value
      * @param dataType
      */
  getFormattedValue: function getFormattedValue(value, dataType) {
    if (bizagi.util.isEmpty(value)) return "";

    // If dataType is integer
    if ([1, 2, 3, 4, 7].includes(dataType)) return value;

    // If dataType is boolean
    if ([5, 17].includes(dataType)) return bizagi.util.formatBoolean(value);

    // If dataType is monetary
    if ([8].includes(dataType)) return bizagi.util.formatMonetaryValue(value);

    // If dataType is real or float
    if ([6, 10, 11].includes(dataType)) return bizagi.util.formatDecimalValue(value);

    // If dataType is date
    if ([12, 13].includes(dataType)) return bizagi.util.formatDateValue(value);

    // Otherwise, If dataType is string
    return bizagi.util.sanitizeHTML(value) || "";
  },

  /***/
  getDesignModeValue: function getDesignModeValue(renderType) {

    // If dataType is integer
    if (renderType == 'number') return '123';
    if (renderType == 'string') return 'abc';
    if (renderType == 'date') return '1/1/1900';
    if (renderType == 'boolean') return 'false';
    if (renderType == 'currency') return '$123';

    return "";
  },

  /** 
      * Customizes render background color
      * @param {} color
      * @returns {}
      */
  changeBackgroundColor: function changeBackgroundColor(color) {
    if (typeof color === "undefined") return this;

    if (color !== "none") {
      this.element.style("background", color, "important");
    } else {
      this.element.css("background", "");
    }

    return this;
  },

  /**
      * Gets jQuery Object to stylize
      * @param {jQuery Object} $context jQuery Object
      */
  getElementsToStylize: function getElementsToStylize($context) {
    return $(".bz-input--readonly", $context);
  } });