/**
 *   Name: Bizagi Render Geolocation control
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for geolocation renders
 */

$.Class.extend("bizagi.rendering.geolocation", {}, {

  /**
                                                      * Constructor
                                                      * @param {} data
                                                      */
  init: function init(data) {
    // Call base
    this._super(data);
    this.geolocation = window.navigator.geolocation;

    var properties = this.properties;
    properties.allowUpdate = bizagi.util.parseBoolean(properties.allowUpdate) === true;

  },

  /**
      * Template method to implement in each children to customize each control
      */
  renderControl: function renderControl() {
    var self = this;
    var template = self.renderFactory.getTemplate("geolocation");

    // Render template
    var html = $.fasttmpl(template, self.properties);
    return html;
  },

  /**
      * Public method to determine if a value is valid or not
      * @param {} invalidElements
      */
  isValid: function isValid(invalidElements) {
    // Call base
    return this._super(invalidElements);
  },

  /**
      * Sets the value in the rendered control
      * @param {} value
      * @returns {}
      */
  setDisplayValue: function setDisplayValue(value) {
    // Set internal value
    this.setValue(value, false);
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      * @returns {}
      */
  configureHandlers: function configureHandlers() {

  },

  /**
      * Sets the value in the rendered control
      * @param {} succes_callback
      * @param {} fail_callback
      */
  getCurrentPosition: function getCurrentPosition(succes_callback, fail_callback) {
    var self = this;

    if (typeof self.geolocation === "undefined") {
      if (fail_callback)
      fail_callback(bizagi.localization.getResource("render-control-unsuported-by-browser").replace("{0}", self.properties.type));
      return;
    }

    var onSuccess = function onSuccess(position) {
      if (succes_callback)
      succes_callback(position);
    };

    var onError = function onError(error) {
      var strMessage = self.getErrorMessage(error);

      if (fail_callback)
      fail_callback(strMessage);
    };

    return self.geolocation.getCurrentPosition(onSuccess, onError);
  },

  /**
      * Get message of error
      * @param error
      * @returns {*|string}
      */
  getErrorMessage: function getErrorMessage(error) {
    var strMessage = "";

    // Check for known errors
    switch (error.code) {
      case error.PERMISSION_DENIED:
      case 1:
        strMessage = bizagi.localization.getResource("render-geolocation-permission-denied");
        break;
      case error.POSITION_UNAVAILABLE:
      case 2:
        strMessage = bizagi.localization.getResource("render-geolocation-position-unavailable");
        break;
      case error.TIMEOUT:
      case 3:
        strMessage = bizagi.localization.getResource("render-geolocation-timeout");
        break;
      default:
        strMessage = "WD: " + bizagi.localization.getResource("render-geolocation-permission-denied");
        break;}


    return strMessage;
  },

  /**
      * Change precision number of coordinates
      * @param data
      * @returns {*}
      */
  performPrecision: function performPrecision(data) {
    var geolocation = data.split(" ") || "";

    if (!geolocation) return data;

    try {
      var latitude = parseFloat(geolocation[0].trim());
      var longitude = parseFloat(geolocation[1].trim());

      return latitude.toPrecision(10) + " " + longitude.toPrecision(10);
    } catch (e) {
      return data;
    }
  } });