$.Class.extend("bizagi.rendering.label", {}, {

  /**
                                                * Update or init the element data
                                                * @param {} data
                                                * @returns {}
                                                */
  initializeData: function initializeData(data) {
    var self = this;

    // Call base
    self._super(data);

    // A label itself is never "required" as other controls
    var properties = self.properties;
    properties.required = false;

    // Set properties
    self.properties.displayType = "label";
    //self.properties.labelAlign = self.originalProperties.labelAlign || "left";
    self.properties.displayName = data.properties.displayName || "";

    if (bizagi.util.parseBoolean(self.properties.allowDinamiclabel)) {
      this.properties.displayName = data.properties.displayName || bizagi.localization.getResource("render-label-default-display-name");
    }

    self.bindingValue();

    // Calculate layout properties
    self.calculateInitialLayoutProperties();
  },

  /**
      * Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    this._super();
    // Just apply display-type: label, align it to the left
    this.changeDisplayOption("label");
    //this.changeLabelAlign(this.properties.labelAlign || "left");

    if (typeof this.configureHelpText == "function")
    this.configureHelpText();
  },

  /**
      * Template method to implement in each device to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this._super();
    // Just apply display-type: label, align it to the left
    this.changeDisplayOption("label");
    this.changeLabelAlign(this.properties.labelAlign || "left");
  },

  /**
      * Public method to determine if a value is valid or not
      * @param {} invalidElements
      */
  isValid: function isValid(invalidElements) {
    // Automatically this render is valid, so we can't check a required label
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @returns {}
      */
  canBeSent: function canBeSent() {
    // This render cannot be sent because it is full ajax
    return false;
  },

  bindingValue: function bindingValue() {
    var self = this;

    if (self.properties.allowDinamiclabel && self.properties.value) {
      if (_typeof(self.properties.value) === "object" && self.properties.value[0]) {
        self.properties.displayName = self.properties.value[0].value;
      } else if (typeof self.properties.value === "string" && self.properties.value !== "") {
        self.properties.displayName = self.performLabelLineBreak(self.properties.value);
      }
    }
  },

  controlValueIsChanged: function controlValueIsChanged() {
    //Labels never change
    return false;
  },

  /**
      * Process the break line character
      * @param value
      * @returns {string}
      */
  performLabelLineBreak: function performLabelLineBreak(value) {
    var label = value || "";

    return label.replace(/\\n/gm, "\n");
  } });