/**
 *   Name: Bizagi Render Button Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for button renders
 */

$.Class.extend("bizagi.rendering.button", {}, {

  /**
                                                 * Update or init the element data
                                                 * @param {} data
                                                 * @returns {}
                                                 */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Fill default properties
    var properties = this.properties;
    properties.caption = properties.caption || "";

    // set default value from normal to value
    properties.displayType = "value";

    // A button cannot be required
    properties.required = false;

    // This flag enable/disable default behaviors of buttons
    // auto save and execute rule
    properties.preventDefault = properties.preventDefault || false;
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {}
      */
  renderControl: function renderControl() {
    var self = this;
    var template = self.renderFactory.getTemplate("button");

    // Render template
    return $.fasttmpl(template, {
      caption: self.properties.caption,
      cssClass: self.properties.cssclass || "",
      editable: self.properties.editable });

  },

  /**
      * Method to render non editable values
      * @returns {}
      */
  renderReadOnly: function renderReadOnly() {
    // Executes the same template than normal render
    return this.renderControl();
  },

  /**
      * Template method to implement in each device to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    // call base
    this._super();

    this.getLabel().addClass('bz-form-label--hidden');
  },

  /**
      * Template method to implement in each device to customize the render behaviour after to be processed
      */
  postRender: function postRender() {
    // call base
    this._super();

    this.getLabel().addClass('bz-form-label--hidden');
  },

  /**
      * Saves the form
      * @returns {}
      */
  saveForm: function saveForm() {
    var form = this.getFormContainer();
    return form.saveForm();
  },

  /**
      * Executes the button action
      * @returns {}
      */
  executeButton: function executeButton() {
    var self = this;
    var properties = self.properties;
    var xpathContext = properties.xpathContext || "";

    if (properties.submitOnChangexpathContextRow) {
      xpathContext = xpathContext ? xpathContext + "." + properties.submitOnChangexpathContextRow : properties.submitOnChangexpathContextRow;
    }

    // Set the loading feedback
    self.getFormContainer().startLoading();

    return self.dataService.multiaction().executeButton({
      idRender: properties.id,
      xpath: properties.xpath || "",
      xpathContext: xpathContext || "",
      idPageCache: properties.idPageCache }).
    always(function () {
      self.getFormContainer().endLoading();
      return arguments ? arguments[0] : undefined;
    });
  },

  /**
      * Refresh the current form
      * @returns {}
      */
  refreshForm: function refreshForm() {
    var form = this.getFormContainer();
    form.refreshForm(this.properties.id);
  },

  /**
      * Process the button actions workflow
      * @returns {}
      */
  processButton: function processButton() {
    var self = this;

    if (self.properties.editable) {
      // Save the form prior opening the dialog
      self.runButtonAction();
    }
  },

  /**
      * Triggers the render change event
      * @param {} params
      * @returns {}
      */
  triggerRenderChange: function triggerRenderChange(params) {
    var self = this;
    params = params || {};

    var pressed = params.pressed || false;

    self.triggerHandler("renderchange", { render: self, pressed: pressed });
  },

  /**
      * Run button actions
      * @returns {}
      */
  runButtonAction: function runButtonAction() {
    var self = this;

    // Save the form prior opening the dialog
    self.getFormContainer().clearValidationMessages();

    self.saveForm().pipe(function () {
      return self.executeButton();
    }).done(function (result) {

      var type = result && result.type ? result.type : "";

      if (result == null || result == true || result.result === "success" || result.type === "success") {
        return self.refreshForm();
      } else if (["validationMessages", "alertMessages"].includes(type)) {
        try {
          if (type === "alertMessages") {
            bizagi.modalError && bizagi.modalError.show(result.messages.join("\n\n"), 'workportal-general-word-information', false);
          } else {
            // Add validation messages
            self.getFormContainer().addValidationMessage(result.messages);
          }
        } catch (e) {
        }
      }
    }).fail(function (error) {

      try {
        // Add error messages
        var error_obj = bizagi.util.getErrorFormatted(error);
        var message = typeof error_obj === "undefined" ? {
          "message": bizagi.localization.getResource("workportal-general-error") } :
        error_obj;

        self.getFormContainer().failHandler(message);
      } catch (e) {
      }

    });

  },

  processLayout: function processLayout(isLabelFormat, format) {
    if (isLabelFormat) {
      this.changeBackgroundColor(format.background);
      this._super(!isLabelFormat, format);
    }
  },

  changeBackgroundColor: function changeBackgroundColor(color) {
    if (typeof color === "undefined") return this;

    if (color !== "none") {
      this.getElementsToStylize(this.getControl()).style("background", color, "important");
    } else {
      this.getElementsToStylize(this.getControl()).css("background", "");
    }

    return this;
  },

  getElementsToStylize: function getElementsToStylize($context) {
    return $("input", $context);
  },

  /**
      *  Method to determine if the render value can be sent to the server or not
      * @returns {}
      */
  canBeSent: function canBeSent() {
    // This render cannot be sent because it is full ajax
    return false;
  },
  /*
     *   Enables submit on change feature for the current render
     */
  configureSubmitOnChange: function configureSubmitOnChange() {
    var self = this;
    var properties = self.properties;
    var renderId = "";

    if (!properties.editable) return;

    // Build action
    properties.dependencies = properties.dependencies || 'all';

    if (properties.submitOnChangexpathContext) {
      renderId = properties.submitOnChangexpathContext + "." + properties.id;
    } else {
      renderId = properties.id;
    }

    var action = {
      "conditions": {
        "expressions": [
        {
          "simple": {
            "renderId": renderId,
            "operator": "changes",
            "argumentType": "const" } }] },




      "commands": [
      {
        "xpath": renderId,
        "command": 'submit-value',
        "argument": properties.dependencies }],


      "dependencies": [renderId],
      "elseCommands": [] };



    // Add action when form ends its rendering
    $.when(self.ready()).done(function () {
      var form = self.getFormContainer();
      form.addSubmitAction(renderId, action);
    });
  } });