/**
 *   Name: Bizagi Rendering Validation controller
 *   Author: Diego Parra
 *   Comments:
 *   -   This script will parse and execute all the validations defined for the form
 */

bizagi.command.controllers.controller.extend("bizagi.command.controllers.validation", {}, {

  /* 
                                                                                            *   Constructor
                                                                                            */
  init: function init(form, validations, canvas) {
    // Enable evalValidation function with console log trace
    this.enableDebug = false;

    // Call base
    this._super(form);

    // Perform notifications
    this.initializeValidations(canvas, validations);
  },

  /**
      * Perform validations component
      * @param canvas
      * @param validations
      */
  initializeValidations: function initializeValidations(canvas, validations) {
    var self = this;
    var validationBox = $('<div />').appendTo(canvas);

    //Notifies that a modal view appear/disappear
    $.widget("ui.bizagi_notifications", $.ui.bizagi_notifications, {
      expand: function expand() {
        this._super();
        self.form.triggerGlobalHandler("modalViewDidAppear", {});
      },
      collapse: function collapse() {
        this._super();
        self.form.triggerGlobalHandler("modalViewDidDisappear", {});
      },
      hide: function hide() {
        this._super();
        self.form.triggerGlobalHandler("modalViewDidDisappear", {});
      } });


    // Apply plugin
    validationBox.bizagi_notifications({
      itemIcon: "ui-icon-circle-close",
      minimized: false,
      clearEnabled: false,
      title: bizagi.localization.getResource("render-form-validations-header-text"),
      device: bizagi.util.detectDevice(),
      orientation: self.form.properties.orientation });


    // Bind click
    validationBox.bind("itemClick", function (e, ui) {
      self.onNotificationClick(ui.data);
    });

    // Build validations
    if (validations) {
      self.buildValidations(validations);
    }

    // Keep references
    self.validationBox = validationBox;
    self.validations = validations || [];
  },

  /* 
     *   Method to incorporate automatic validation rules 
     */
  buildValidations: function buildValidations(validations) {
    var self = this;
    var fnValidations = self.buildAllValidations(validations);

    self.form.bind("rendervalidate", function () {
      return fnValidations(self);
    });
  },

  /* 
     *   Method to add a simple error message in the validation box
     */
  showErrorMessage: function showErrorMessage(message) {
    bizagi.showErrorAlertDialog = false;
    this.showValidationMessage(message, null, "ui-icon-circle-close", "ui-state-error");
  },

  /* 
     *   Method to add a simple alert message in the validation box
     */
  showAlertMessage: function showAlertMessage(message) {
    this.showValidationMessage(message, null, "ui-icon-alert", "ui-state-highlight");
  },

  /* 
     *   Method to add a simple info message in the validation box
     */
  showInfoMessage: function showInfoMessage(message) {
    this.showValidationMessage(message, null, "ui-icon-info", "ui-state-highlight");
  },

  /* 
     *   Method to add a simple validation message in the validation box
     */
  showValidationMessage: function showValidationMessage(message, focus, icon, itemAdditionalClass, type, renderId) {
    var self = this;
    var form = self.form;
    var render = null;

    if (renderId) {
      render = form.getRenderById(renderId) || null;
    } else {
      render = focus ? form.getRender(focus) : null;
    }

    bizagi.showErrorAlertDialog = false;

    // Method to perform validations after the render has been changed, or the notifications has been clicked
    var performValidationFn = function performValidationFn() {
      // First hide and clear validation box
      self.clearValidationMessages();

      var lastActionButton = form ? form.getLastActionButton() : {};
      var skipValidation = lastActionButton && lastActionButton.action === "nextWithoutValidations";

      if (!skipValidation) {
        // Run validations
        self.validating = true;
        self.performValidations();
        self.validating = false;
      }

      // Attempt to hide if there are no validations
      if (self.countValidationMessages() == 0) {
        self.hideValidationMessages();
        self.form.triggerGlobalHandler("modalViewDidDisappear", {});
      } else {
        self.form.triggerGlobalHandler("modalViewDidAppear", {});
      }

      //form.triggerGlobalHandler("modalViewDidDisappear", {});
    };
    self.form.triggerGlobalHandler("modalViewDidAppear", {});
    // Add message
    self.validationBox.bizagi_notifications("addNotification", message, focus, icon, itemAdditionalClass, performValidationFn, type, renderId);

    if (render) {
      // Add error message to render
      render.setValidationMessage(message);

      if (!bizagi.util.isIE8()) {

        // Bind event when render change
        if (!self.validating) render.bind("rendervalidate", function () {
          performValidationFn();
          if (render.properties.retype != "double") {
            render.unbind("rendervalidate");
          }
        });
      }
    }
  },

  /*
     *   Count the number of validation messages
     */
  countValidationMessages: function countValidationMessages() {
    return this.validationBox.bizagi_notifications("count");
  },

  /*
     *   Remove all the validations
     */
  clearValidationMessages: function clearValidationMessages(params) {
    var validationsBoxes = $(".ui-bizagi-notifications-container", "body").not(".ui-bizagi-log-container");

    return validationsBoxes.bizagi_notifications("clearAll", undefined, params);
  },

  /*
     *   Hide the validation notifications box
     */
  hideValidationMessages: function hideValidationMessages() {
    return this.validationBox.bizagi_notifications("hide");
  },

  /*
     *   Show the validation notifications box
     */
  showValidationMessages: function showValidationMessages() {
    return this.validationBox.bizagi_notifications("show");
  },

  /*
     *   Expand the notification box
     */
  expandNotificationBox: function expandNotificationBox(autofocus) {
    // Select first error and expand notifications
    if (autofocus !== false) this.validationBox.bizagi_notifications("autoFocusFirstError");
    return this.validationBox.bizagi_notifications("expand");
  },

  isExpandedNotificationBox: function isExpandedNotificationBox() {
    return this.validationBox.bizagi_notifications("isExpanded") && this.validationBox.bizagi_notifications("isContentVisible");
  },

  /*
     *   Run all validation rules in the form container
     */
  performValidations: function performValidations(invalidElements) {
    var self = this;

    // First hide and clear validation box
    self.clearValidationMessages();
    self.validationBox.bizagi_notifications("initBatch");

    // Run render validations
    var bRendersValid = self.validateRenders(invalidElements);

    // Run form validations
    var bFormValid = self.validations.length > 0 ? self.form.triggerHandler("rendervalidate") : true;
    self.validationBox.bizagi_notifications("endBatch");

    // Show validations if invalid
    if (!bFormValid || !bRendersValid) {
      self.validationBox.bizagi_notifications("show");
      return false;
    }

    return true;
  },

  /* PRIVATE METHODS */

  /*  
                        *   Construct validations javascript rules
                        */
  buildAllValidations: function buildAllValidations(validations) {
    var self = this;
    var sJSCode = "";

    for (var i = 0; i < validations.length; i++) {

      var isEnabled = validations[i].enabled != undefined ? validations[i].enabled : true;
      if (isEnabled) {
        // If validation is not "valid" we can omit that instruction
        try {
          sJSCode += self.buildValidation(validations[i].conditions, validations[i].validationCommand);
        } catch (e) {
          if (e.message != "RenderNotFoundException") {
            bizagi.logError("Validation cannot be parsed: " + e.message, validations[i]);
          }
        }
      }
    }

    // Build final function
    var sStatement = "var baTmpFn = function(self, params){\r\n var bValid = true;\r\n " + sJSCode + "\r\n return bValid;}; baTmpFn";

    // Return interpreted function
    return self.evalValidation(sStatement);
  },

  /*
     *   Build a single javascript validation rule
     */
  buildValidation: function buildValidation(conditions, validationCommand) {
    var self = this;
    var sJsCondition = "";
    var sJsValidation = "";

    // Detect grid references
    var gridReference = self.searchForGridReference(conditions);
    var bGridDetected = gridReference != null;
    var validateEditability = typeof bizagi.override.validateEditabilityOnValidations != "undefined" ? bizagi.override.validateEditabilityOnValidations : true;

    // Build Conditions
    sJsCondition += "( " + self.buildConditions(conditions, bGridDetected, validateEditability) + " )";

    // Fix message for quotes and break lines
    validationCommand.message = validationCommand.message.replace(/'/g, "\\'");
    validationCommand.message = validationCommand.message.replace(/\n/g, " ");

    // Build Message
    var focus = validationCommand.focus || "";
    if (bGridDetected) focus = gridReference;
    if (validationCommand.tokens)
    sJsValidation += "self.showValidationMessage(' " + validationCommand.message + "', '" + focus + "', JSON.parse('" + JSON.encode(validationCommand.tokens) + "')); bValid = false;\r\n";else

    sJsValidation += "self.showValidationMessage(' " + validationCommand.message + "', '" + focus + "'); bValid = false;\r\n";

    // Build final statement
    if (!bGridDetected) {


      return "if (" + sJsCondition + "){\r\n " + sJsValidation + " \r\n}";
    } else {
      var sGridStatement = "var grid = self.form.getRender('" + gridReference + "');\r\n";
      sGridStatement += "if(grid === null) return true;\r\n";
      sGridStatement += "var data = grid.getIndexes();\r\n";
      sGridStatement += "var bGridValid = true;\r\n";
      sGridStatement += "$.each(data, function(_, i) {\r\n";
      sGridStatement += "if (" + sJsCondition + "){\r\n bGridValid = false; \r\n}";
      sGridStatement += "});\r\n";
      sGridStatement += "if (bGridValid == false){\r\n " + sJsValidation + "\r\n}";
      return sGridStatement;
    }
  },

  /* 
     *   Method triggered when the validation item has been clicked
     */
  onNotificationClick: function onNotificationClick(data) {
    var self = this;
    var form = self.form;
    var xpath = data.focus;
    var renderId = data.id;

    if (xpath) {
      var render = renderId ? form.getRenderById(renderId) : form.getRender(xpath);

      if (render) {
        render.focus();
      }
    }
  },

  /* 
     *   Method to loop
     */
  validateRenders: function validateRenders(invalidElements) {
    var self = this;
    invalidElements = invalidElements || [];

    // Do a check
    var bValid = self.form.isValid(invalidElements);
    if (!bValid) {
      invalidElements.forEach(function (element) {
        if (element.id) {
          // Parameters: message, xpath, icon, itemAdditionalClass, type, renderId
          self.showValidationMessageById(element.message, element.xpath, element.id);
        } else if (element.message || element.xpath) {
          self.showValidationMessage(element.message, element.xpath);
        }
      });
    }

    return bValid;
  },

  /*
     *	Eval validation string, for debug purposes
     */
  evalValidation: function evalValidation(command) {
    var self = this;

    if (self.enableDebug && console != undefined) {
      console.log(command);
    }

    return eval(command);
  },

  /**
      * Method to add a simple validation message in the validation box
      * @param message
      * @param xpath
      * @param renderId
      */
  showValidationMessageById: function showValidationMessageById(message, xpath, renderId) {
    this.showValidationMessage(message, xpath, undefined, false, undefined, renderId);
  } });