/**
 *   Name: Bizagi Render Letter Class
*   Author: Diego Parra
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for letter renders
 */

$.Class.extend("bizagi.rendering.letter", {}, {

  /**
                                                 * Update or init the element data
                                                 * @param {} data
                                                 */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Create a variable to check if the letter has been opened when editable
    this.letterOpened = false;
  },


  /**
      * Template method to implement in each children to customize each control
      * @returns {}
      */
  renderControl: function renderControl() {
    var template = this.renderFactory.getTemplate("letter");

    // Render template
    return $.fasttmpl(template);
  },


  /**
      * Method to render non editable values
      */
  renderReadOnly: function renderReadOnly() {
    var template = this.renderFactory.getTemplate("letter.readonly");

    // Render template
    return $.fasttmpl(template);
  },

  /**
      * Returns the letter content from the server
      */
  getLetterContent: function getLetterContent() {
    // Set flag to true
    this.letterOpened = true;

    return this.dataService.getLetterContent({
      url: this.properties.getUrl,
      idRender: this.properties.id,
      xpath: this.getXpath(),
      xpathContext: this.getContextXpath(),
      idPageCache: this.properties.idPageCache });

  },

  /**
      * Sends the content to the server in order to save
      * @param {} content
      */
  saveLetterContent: function saveLetterContent(content) {
    return this.dataService.saveLetterContent({
      url: this.properties.saveUrl,
      idRender: this.properties.id,
      xpath: this.getXpath(),
      xpathContext: this.getContextXpath(),
      idPageCache: this.properties.idPageCache,
      content: content });

  },

  /**
      * Returns the letter content from the server
      */
  getCanGenerateLetter: function getCanGenerateLetter() {
    return this.dataService.getCanGenerateLetter({
      idRender: this.properties.id,
      xpath: this.getXpath(),
      xpathContext: this.getContextXpath(),
      idPageCache: this.properties.idPageCache });

  },

  /**
      * Saves the form
      */
  saveForm: function saveForm() {
    var self = this;
    var form = self.getFormContainer();
    return form.saveForm();
  },

  isValid: function isValid(invalidElements) {
    var self = this;
    var properties = self.properties;
    var message;

    // Don't check non-editable renders
    if (bizagi.util.parseBoolean(properties.visible) == false) return true;

    // Don't check non-editable renders
    if (bizagi.util.parseBoolean(properties.editable) == false) return true;

    // Clear error message
    self.setValidationMessage("");

    // Check required
    if (properties.required) {
      if (self.letterOpened == false) {
        message = self.getResource("render-letter-required-text").replaceAll("#label#", properties.displayName);
        invalidElements.push({ xpath: properties.xpath, message: message });
        return false;
      }
    }

    return true;
  },

  /**
      * Returns the current xpath
      */
  getXpath: function getXpath() {
    return this.properties.xpath;
  },

  /**
      * Returns the current context xpath
      */
  getContextXpath: function getContextXpath() {
    return this.properties.xpathContext;
  } });