/*
 *   Name: Bizagi Section Container Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define a section container class for all devices
 */

$.Class.extend(
"bizagi.rendering.section",
{},
{
  /**
   * Constructor
   * @param {object} params - section information
   */
  initializeData: function initializeData(params) {
    var self = this;

    // Call base
    self._super(params);

    // Default properties
    var properties = self.properties;

    properties.helpText = properties.helpText || properties.displayName;
    properties.displayAs = properties.displayAs || "section";
  },

  /**
      * Render the trigger of the section.
      * @returns string with html code.
      */
  renderContainer: function renderContainer() {
    var self = this;
    var properties = this.properties;
    var template = this.renderFactory.getTemplate("section-header");

    // Render the section trigger
    var html = $.fasttmpl(template, {
      displayName: properties.displayName,
      editable: properties.editable,
      orientation: properties.orientation,
      uniqueId: properties.uniqueId,
      isDesign: self.getMode() !== "execution",
      messageValidation: properties.messageValidation,
      guid: properties.guid });

    this.sectionContent = $(this.renderSectionContent())[0];
    return html;
  },

  /**
      * Render the content of the section.
      * Overwritable on devices.
      */
  renderSectionContent: function renderSectionContent() {},

  /**
                                                             * Return the breadcrumb of the section.
                                                             * @returns An array with breadcrumb. For example: ["activity", "parent section", "child section"]
                                                             */
  getBreadcrumb: function getBreadcrumb() {
    var container = this;
    var breadCrumb = [];

    while (container) {
      var properties = container.properties || {};
      var params = container.params || {};

      if (properties.type === "section") {
        var displayName = properties.displayName ? properties.displayName : "";
        breadCrumb.unshift(displayName);
      } else if (params.type === "form") {
        breadCrumb.unshift(this.getResourceByKey("render-section-breadcrumb-main-form", "Main form"));
      }
      container = container.parent;
    }
    return breadCrumb;
  },

  /**
      * Gets the display name of the previous section or form.
      * @returns String with the display name
      */
  getParentDisplayName: function getParentDisplayName() {
    var self = this;
    var parent = self.getSectionParent(self.parent);
    var displayName = "";

    //TODO Improve this code implementation 
    if (parent.properties.type == "form") {
      if (parent.properties.formtype && parent.properties.formtype == "startform") {
        displayName = parent.params && parent.params.originalParams && parent.params.originalParams.displayName ? parent.params.originalParams.displayName : "";
      } else
      {
        displayName = parent.properties.displayName || parent.params.displayName || parent.params.radNumber || parent.params.idCase;
      }
    } else {
      displayName = parent.properties.displayName || "";
    }

    return displayName;
  },

  /**
      * Gets the section's parent.
      * @returns Object parent
      */
  getSectionParent: function getSectionParent(parent) {
    if (parent.properties.type === "section" || !parent.parent) {
      return parent;
    } else {
      return this.getSectionParent(parent.parent);
    }
  },

  /**
      * Gets if the section's  orientation is RTL.
      * @returns Object
      */
  getSectionOrientationRTL: function getSectionOrientationRTL(obj) {
    if (obj && obj.properties && obj.properties.orientation && obj.properties.orientation === "ltr") return false;
    if (obj && obj.properties && obj.properties.orientation && obj.properties.orientation === "rtl") return true;
    if (obj.parent) return this.getSectionOrientationRTL(obj.parent);
    return false;
  } });