/**
 *   Name: Bizagi Render Upload Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for uploads
 */

$.Class.extend("bizagi.rendering.upload", {}, {
  /***
                                                 * Update or init the element data
                                                 * @param data
                                                 */
  initializeData: function initializeData(data) {
    var self = this;

    // Call base
    self._super(data);

    var form = self.getFormContainer();

    // Fill default properties
    var properties = this.properties;

    var maxFileSizeOfProperty = Number(properties.maxSize) || 0;
    var maxFileSizeOfUser = typeof BIZAGI_SETTINGS !== "undefined" && typeof BIZAGI_SETTINGS.UploadMaxFileSize !== "undefined" ? Number(BIZAGI_SETTINGS.UploadMaxFileSize) : 4091904;

    properties.maxSize = maxFileSizeOfProperty || maxFileSizeOfUser;
    properties.maxfiles = Number(properties.maxfiles) || 999;
    properties.validExtensions = properties.validExtensions || "";

    if (properties.validExtensions.length > 0 && properties.validExtensions.indexOf(".") < 0) {
      var singleExtensions = properties.validExtensions.replace(/\ /gi, '').split(";");
      var defExtensions = [];

      for (var i = 0; i < singleExtensions.length; i++) {
        if (singleExtensions[i].length > 0) {
          defExtensions.push("*." + singleExtensions[i]);
        } else {
          //Removes the empty element
          var index = singleExtensions.indexOf(singleExtensions[i]);
          singleExtensions.splice(i, 1);
        }
      }

      properties.validExtensions = defExtensions.join(";");
    }

    properties.addUrl = properties.addUrl || self.dataService.getUploadAddUrl();
    properties.deleteUrl = properties.deleteUrl || undefined;
    properties.editable = bizagi.util.parseBoolean(properties.editable) || false;
    properties.allowDelete = bizagi.util.parseBoolean(properties.allowDelete) !== null && properties.editable ? bizagi.util.parseBoolean(properties.allowDelete) : false;

    properties.contexttype = form.params && form.params.data && form.params.data.contextType ? form.params.data.contextType : "";
    properties.allowSendInMail = properties.allowSendInMail || false;

    // Set files property
    self.files = properties.value || [];
    self.filesCount = self.files.length;
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {*|jQuery}
      */
  renderControl: function renderControl() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("upload");

    // Render template
    var html = $.fasttmpl(template, {
      xpath: bizagi.util.encodeXpath(self.getUploadXpath()),
      editable: properties.editable,
      haveFiles: self.filesCount !== 0,
      noFiles: self.filesCount === 0,
      allowSendInMail: properties.allowSendInMail });


    // Render current children
    var items = "";
    for (var i = 0; i < self.filesCount; i++) {
      var file = { id: self.files[i][0], fileName: self.files[i][1] };
      var item = self.renderUploadItem(file);

      items += item;
    }

    html = self.replaceFilesHtml(html, items);
    return html;
  },

  /**
      * Method to render non editable values
      * @returns {*|*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    return this.renderControl();
  },

  /**
      * Renders a single upload item
      * @param file
      * @returns {jQuery|*}
      */
  renderUploadItem: function renderUploadItem(file) {
    var self = this;
    var mode = self.getMode();
    var itemTemplate = self.getTemplateItemName();
    var template = self.renderFactory.getTemplate(itemTemplate);
    var form = self.getFormContainer();
    var printVersion = form.params && form.params.printversion;

    // Don't render urls when not running in execution mode
    var url = !self.isExecutionMode() ? "" : self.buildItemUrl(file); // TODO: Review if need it: javascript:void(0);

    var html = $.fasttmpl(template, {
      id: file.id,
      mode: mode,
      url: url,
      allowDelete: self.properties.allowDelete,
      filename: bizagi.util.decodeURI(file.fileName),
      editable: self.properties.editable,
      printVersion: printVersion || false });


    return html;
  },

  /**
      * Builds the upload item url
      * @param file
      * @returns {*}
      */
  buildItemUrl: function buildItemUrl(file) {
    var self = this;
    var properties = self.properties;
    var form = self.getFormContainer();

    return self.dataService.getUploadFileUrl({
      idRender: properties.id,
      xpath: self.getUploadXpath(),
      xpathContext: properties.xpathContext,
      idPageCache: properties.idPageCache,
      fileId: file.id,
      sessionId: form.properties.sessionId,
      contexttype: properties.contexttype });

  },

  /**
      * Replaces a {{files}} tag in the container for the specified "replace" element
      * @param html
      * @param replace
      * @returns {*}
      */
  replaceFilesHtml: function replaceFilesHtml(html, replace) {
    return html.replace("{{files}}", replace);
  },

  /**
      * Removes a file from the upload control
      * @param item
      * @param fileId
      * @returns {*} Returns a deferred
      */
  deleteUploadItem: function deleteUploadItem(item, fileId) {
    var self = this;
    var properties = self.properties;
    var xpath = self.getUploadXpath() + (bizagi.util.isNumeric(fileId) ? "[id=" + fileId + "]" : "[id='" + fileId + "']");

    return self.dataService.deleteUploadFile({
      url: properties.deleteUrl,
      idRender: properties.id,
      xpath: xpath,
      xpathContext: properties.xpathContext,
      idPageCache: properties.idPageCache,
      contexttype: properties.contexttype }).
    pipe(function () {

      // Subtract counter
      self.filesCount = self.filesCount - 1;

      // Remove from local collection
      self.files = $.grep(self.files, function (fileItem) {
        return fileItem[0] != fileId;
      });
    });
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @returns {boolean}
      */
  canBeSent: function canBeSent() {
    // This render cannot be sent because it is full ajax
    return false;
  },

  /**
      * Sets the internal value
      * @param value
      * @param triggerEvents
      */
  setValue: function setValue(value, triggerEvents) {
    var self = this;

    // Set files property
    self.files = value || []; // Dont remove this line
    self.filesCount = self.files.length;

    // Call base
    self._super(value, triggerEvents);
  },

  /**
      * Returns the internal value
      * @returns {*}
      */
  getValue: function getValue() {
    return this.files;
  },

  /**
      * Public method to determine if a value is valid or not
      * @param invalidElements
      * @returns {boolean}
      */
  isValid: function isValid(invalidElements) {
    var self = this;
    var properties = self.properties;
    var message;
    var bValid = true;

    // Clear error message
    self.setValidationMessage("");

    // Check required
    if (properties.required && properties.editable) {
      var newRow = self.grid && self.grid.isFromCreatedRow.apply(self);

      if (self.filesCount <= 0 && !newRow) {
        var inlineEdit = self.grid ? self.grid.properties.inlineEdit : true;

        if (inlineEdit) {
          message = self.getResource("render-required-upload").replaceAll("#label#", properties.displayName);
          invalidElements.push({ xpath: properties.xpath, message: message });
          bValid = false;
        }
      }
    }

    return bValid;
  },

  /**
      * Build add params to send to the server
      */
  buildAddParams: function buildAddParams() {
    var self = this;
    var properties = self.properties;
    var form = self.getFormContainer();
    var data = {};
    var prefix = self.Class.BA_CONTEXT_PARAMETER_PREFIX;

    data[prefix + "xpath"] = self.getUploadXpath();
    data[prefix + "idRender"] = properties.id;
    data[prefix + "xpathContext"] = properties.xpathContext;
    data[prefix + self.Class.BA_PAGE_CACHE] = properties.idPageCache;
    data[self.Class.BA_ACTION_PARAMETER_PREFIX + "sessionId"] = form.properties.sessionId;
    data[prefix + "contexttype"] = properties.contexttype;

    try {
      if (typeof BIZAGI_SESSION_NAME !== "undefined") {
        data[BIZAGI_SESSION_NAME] = form.properties.sessionId;
      } else {
        data["JSESSIONID"] = form.properties.sessionId;
      }
    } catch (e) {
      data["JSESSIONID"] = form.properties.sessionId;
    }

    return data;
  },

  /**
      * Returns the xpath to be used
      * @returns {*}
      */
  getUploadXpath: function getUploadXpath() {
    return this.properties.xpath;
  },

  /**
      * Returns the xpathContext to be used
      * @returns {*}
      */
  getUploadXpathContext: function getUploadXpathContext() {
    return this.getXpathContext() || this.properties.xpathContext;
  },

  checkRequired: function checkRequired(invalidElements) {
    var self = this;
    var properties = self.properties;

    if (bizagi.util.parseBoolean(properties.required)) {
      if (self.grid && self.grid.properties.inlineAdd === true) {
        if (!self.hasValue() && !self.getControl().parents("tr").data("new-row")) {
          self.setRenderRequired(invalidElements, properties);
          return false;
        }
      } else {
        if (!self.hasValue()) {
          self.setRenderRequired(invalidElements, properties);
          return false;
        }
      }
    }

    return true;
  },

  setRenderRequired: function setRenderRequired(invalidElements, properties) {
    var self = this;
    var message = self.getResource("render-required-text").replaceAll("#label#", properties.displayName);
    invalidElements.push({ xpath: properties.xpath, message: message });
  },

  /**
      * Templates
      */
  getTemplateItemName: function getTemplateItemName() {
    return "uploadItem";
  },

  /**
      * Method to perform the format to value
      * @param value
      * @returns {((*)[]|*)[]}
      */
  formatValue: function formatValue(value) {
    var validateObjectStructure = function validateObjectStructure(item) {
      return !!(item.uploadId && item.fileName);
    };
    var validateArrayStructure = function validateArrayStructure(item) {
      return !!(item[0] && item[1]);
    };

    return (value || []).reduce(function (result, item) {
      var isFormatValid = false;
      var validator = bizagi.util.bzValidator(item).
      add(validateObjectStructure).
      validate(function (response) {
        isFormatValid = response.errors && !response.errors.length;
        if (isFormatValid) {
          result.push([item.uploadId, item.fileName]);
        }
      });

      if (!isFormatValid) {
        validator.add(validateArrayStructure, "The value is not in a valid format").
        validate(function (response) {
          if (response.errors && response.errors.length > 0) {
            console.warn(response.errors);
          } else {
            result.push([item]);
          }
        });
      }

      return result;
    }, []);
  } });