$.Class.extend("bizagi.rendering.date", {}, {
  /*
                                               *   Update or init the element data
                                               */
  initializeData: function initializeData(data) {
    var self = this;
    var ud = bizagi.util.dateFormatter;

    // Call base
    this._super(data);

    // Fill default properties
    var properties = self.properties;
    var dateFormat = self.getResource("dateFormat");
    var timeFormat = self.getResource("timeFormat");

    // We will hold here the value to display
    properties.displayValue = "";
    properties.showTime = properties.showTime !== undefined ? bizagi.util.parseBoolean(properties.showTime) : false;
    properties.dateFormat = properties.dateFormat || dateFormat;
    properties.timeFormat = properties.timeFormat || timeFormat;
    properties.fullFormat = properties.dateFormat;

    // Check if it has the showtime property
    if (properties.showTime) {
      if (properties.fullFormat.search(/[h|H]+/gi) === -1) {
        properties.fullFormat = properties.fullFormat + " " + properties.timeFormat;
      }
    }

    // Read always parameter from server in invariant format
    if (properties.value) {
      var date = ud.getDateFromInvariant(properties.value, properties.showTime);
      // Format date to current format
      if (date != 0) {
        // Value must always hold the value with invariant format
        self.value = properties.value;
        var datePickerRegional = bizagi.localization.getResource("datePickerRegional");
        properties.displayValue = bizagi.util.dateFormatter.formatDate(date, properties.fullFormat, datePickerRegional);
      } else {
        self.value = null;
      }
    }

    // Fix dateFormat for datepicker
    dateFormat = self.getDateFormat(properties.fullFormat);

    var indexDateTimeSeparation = properties.fullFormat.substring(0, properties.fullFormat.indexOf(":")).lastIndexOf(" ");
    if (!properties.showTime)
    properties.fullFormat =
    indexDateTimeSeparation > 0 ? properties.fullFormat.substring(0, indexDateTimeSeparation) : properties.fullFormat;

    properties.datePickerFormat = dateFormat.datePickerFormat;
    properties.timePickerFormat = dateFormat.timePickerFormat;

    self.i18n = bizagi.localization.getResource("datePickerRegional");
  },

  /*
      *   Template method to implement in each children to customize each control
      */
  renderControl: function renderControl() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("date");
    var fullFormat = properties.fullFormat;

    // Render template
    var html = $.fasttmpl(template, {
      id: properties.id,
      showTime: properties.showTime,
      value: properties.value,
      editable: properties.editable,
      dateFormat: bizagi.override && bizagi.override.uppercaseDatePlaceholder === true ? fullFormat.toUpperCase() : fullFormat,
      rtl: properties.orientation == "rtl" });

    return html;
  },

  /*
      *   Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers(intHandler, isNow) {
    var self = this;
    var properties = self.properties;

    // Set control references
    var dateControl = self.getDateControl();
    var timeControl = self.getTimeControl();

    // Attach change event
    dateControl.bind("change", function () {
      self.onChangeHandler();
    });

    if (properties.showTime) {
      timeControl.bind("change", function (e, isNow) {
        if (self.updatingTime) {
          delete self.updatingTime;
          return;
        }
        self.onChangeHandler(isNow);
      });
    }
  },

  /*
      *   Formats the date time value to the hidden control
      */
  onChangeHandler: function onChangeHandler() {
  },

  getDisplayValue: function getDisplayValue(value) {
    var self = this;
    var properties = self.properties;
    var displayValue;

    if (!properties.displayValue && value) {
      var date = bizagi.util.dateFormatter.getDateFromInvariant(value, properties.showTime);
      var datePickerRegional = bizagi.localization.getResource("datePickerRegional");
      displayValue = bizagi.util.dateFormatter.formatDate(date, properties.fullFormat, datePickerRegional);
    } else {
      displayValue = "";
    }

    // We can return the display value isntance
    return properties.displayValue || displayValue || "";
  },

  /*
      *   Add the render data to the given collection in order to send data to the server
      */
  collectData: function collectData(renderValues) {
    var self = this;
    var properties = self.properties;

    // Add the render value
    var xpath = properties.xpath;
    var value = self.getValue();

    if (self.controlValueIsChanged()) {
      // Filter by valid xpaths and valid values
      if (!bizagi.util.isEmpty(xpath)) {
        if (!bizagi.util.isEmpty(value)) {
          // Formats the value in full invariant (with time) in order to send to the server
          var date = bizagi.util.dateFormatter.getDateFromInvariant(value, properties.showTime);
          if (typeof date != "undefined")
          if (date !== 0) {
            if (!properties.showTime) {
              date.setHours(0, 0, 0, 0);
            }
            renderValues[properties.xpath] = bizagi.util.dateFormatter.formatInvariant(date, true);
          }
        } else if (value === "") {
          renderValues[properties.xpath] = "";
        }
      }
    }
  },

  /*
      *   Gets the date control
      */
  getDateControl: function getDateControl() {
  },

  /*
      *   Gets the time control
      */
  getTimeControl: function getTimeControl() {
  },

  /*
      * Public method to determine if a value is valid or not
      */
  isValid: function isValid(invalidElements) {
    var self = this,
    properties = self.properties;

    // Call base
    var bValid = this._super(invalidElements);
    var value = self.getValue();
    if (value) {
      if (bizagi.util.parseBoolean(properties.editable) === true) {
        value = self.getNormalizeDate(bizagi.util.dateFormatter.getDateFromInvariant(value, properties.showTime));
        var message;

        // Check that we have a valid data
        if (value != 0) {
          // Check min value
          if (properties.minValue && (typeof properties.minValue === "string" || typeof properties.minValue == "number")) {
            var minValue = self.getNormalizeDate(
            typeof properties.minValue == "string" ?
            bizagi.util.dateFormatter.getDateFromInvariant(properties.minValue, properties.showTime) :
            new Date(properties.minValue));


            if (value < minValue) {
              message = this.getResource("render-date-minimum-validation").
              replaceAll("#label#", properties.displayName).
              replaceAll("#minValue#", bizagi.util.dateFormatter.formatDate(minValue, properties.fullFormat));
              invalidElements.push({
                xpath: properties.xpath,
                message: message });

              bValid = false;
            }
          }

          // Check max value
          if (properties.maxValue && (typeof properties.maxValue == "string" || typeof properties.maxValue == "number")) {
            var maxValue = self.getNormalizeDate(
            typeof properties.maxValue == "string" ?
            bizagi.util.dateFormatter.getDateFromInvariant(properties.maxValue, properties.showTime) :
            new Date(properties.maxValue));


            if (value > maxValue) {
              message = this.getResource("render-date-maximum-validation").
              replaceAll("#label#", properties.displayName).
              replaceAll("#maxValue#", bizagi.util.dateFormatter.formatDate(maxValue, properties.fullFormat));
              invalidElements.push({
                xpath: properties.xpath,
                message: message });

              bValid = false;
            }
          }
        }
      }
    }
    return bValid;
  },

  /**
      * Translate standar date format to jquery date picker
      * @property {string} format Control's date format
      * @returns {JSON object} A JSON Object whith the date and time format
      */
  getDateFormat: function getDateFormat(format) {
    var datePickerFormat = format;
    var timePickerFormat = undefined;
    var init;

    if (format.search("h") != -1 || format.search("H") != -1) {
      if (format.search("h") != -1) {
        init = format.search("h");
      } else {
        init = format.search("H");
      }

      datePickerFormat = format.substring(0, init - 1);
      timePickerFormat = format.substr(init);
    }

    // Fix month
    if (datePickerFormat.indexOf("MMMM") >= 0) {
      datePickerFormat = datePickerFormat.replaceAll("MMMM", "MM");
    } else if (datePickerFormat.indexOf("MMM") >= 0) {
      // Month name short
      datePickerFormat = datePickerFormat.replaceAll("MMM", "M");
    } else if (datePickerFormat.indexOf("MM") >= 0) {
      // Month two digits
      datePickerFormat = datePickerFormat.replaceAll("MM", "mm");
    } else if (datePickerFormat.indexOf("M") >= 0) {
      // Month one digits
      datePickerFormat = datePickerFormat.replaceAll("M", "m");
    }

    // Fix years
    if (datePickerFormat.search("yyyy") != -1) {
      datePickerFormat = datePickerFormat.replaceAll("yyyy", "yy");
    } else {
      if (datePickerFormat.search("yy") != -1) {
        datePickerFormat = datePickerFormat.replaceAll("yy", "y");
      }
    }

    datePickerFormat = datePickerFormat.replaceAll("dddd", "DD"); // Fix days

    return {
      datePickerFormat: datePickerFormat,
      timePickerFormat: timePickerFormat };

  },

  /*
      *   Template method to implement in each device to customize each render after processed
      */
  postRender: function postRender() {
    var self = this;
    var properties = self.properties;
    // Call base
    this._super();
    if (typeof properties.maxValue == "undefined")
    properties.maxValue = "12/31/" + parseInt(new Date().getFullYear() + 200).toString() + " 00:00:00"; //Current year + 200 IE or 9999
    if (typeof properties.minValue == "undefined")
    properties.minValue = "01/01/" + parseInt(new Date().getFullYear() - 200).toString() + " 00:00:00"; //Current year - 200 IE or 1753 for SQL Server compat
  },

  /**
      * Sets milliseconds to 0 in date
      * @param {Date} date date to be treated
      * @returns {Date} date converted with milliseconds 0
      */
  getNormalizeDate: function getNormalizeDate(date) {
    var self = this;
    if (date != 0 && date != null) {
      if (!self.properties.showTime) {
        date.setHours(0, 0, 0, 0);
      } else date.setMilliseconds(0);
    }
    return date;
  },

  /**
      * Configure the timeformat
      */
  getTimeFormat: function getTimeFormat(format) {
    return format.replaceAll("m", "i").replaceAll("tt", "A");
  },

  /**
      * Sets value to the control
      * @param {Date} date date to asign
      * @returns {}
      */
  setValueFromCalendarButtons: function setValueFromCalendarButtons(date) {
    var self = this;
    if (date) date = self.getNormalizeDate(date);else
    {
      value = null;
      self.setValue("");
      self.setDisplayValue("");
    }
  },

  /*
      *   Changes the render min value
      */
  changeMinValue: function changeMinValue(value) {
    var self = this;
    var properties = self.properties;
    var ud = bizagi.util.dateFormatter;

    // Set value in control
    if (value && properties.editable) {
      if (typeof value === "number") {
        value = ud.formatInvariant(self.getNormalizeDate(new Date(value)), properties.showTime); // Set as invariant
      } else {
        value = self.getNormalizeDate(_typeof(value) === "object" ? value : ud.getDateFromInvariant(value, properties.showTime));
        value = ud.formatInvariant(value);
      }
      properties.minValue = value;
    }
  },

  /*
      *   Changes the render max value
      */
  changeMaxValue: function changeMaxValue(value) {
    var self = this;
    var properties = self.properties;
    var ud = bizagi.util.dateFormatter;

    // Set value in control
    if (value && properties.editable) {
      if (typeof value === "number") {
        value = ud.formatInvariant(self.getNormalizeDate(new Date(value)), properties.showTime); // Set as invariant
      } else {
        value = self.getNormalizeDate(_typeof(value) === "object" ? value : ud.getDateFromInvariant(value, properties.showTime));
        value = ud.formatInvariant(value);
      }
      properties.maxValue = value;
    }
  } });