/**
 *   Name: Bizagi Render Radio Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for radio renders
 */

$.Class.extend("bizagi.rendering.radio", {}, {

  /**
                                                * Update or init the element data
                                                * @param data
                                                */
  initializeData: function initializeData(data) {
    var self = this;
    // Call base
    this._super(data);

    // Fill default properties
    var properties = this.properties;
    properties.horizontal = bizagi.util.parseBoolean(properties.horizontal) || false;

    // Set a random identifier to fix a problem when using radio buttons inside grids and the plugin mismatch the elements because all of them have the same name
    properties.unique = bizagi.util.randomNumber(1000, 1000000);
  },

  /**
      * Gets the template used by the combo render
      * Can be overridden in subclasses to reuse logic and just change the template
      * @returns {string}
      */
  getTemplateName: function getTemplateName() {
    return "radio";
  },

  /**
      * Determines if we need to show the empty node or not
      * Can be overridden to change behaviour
      * @returns {boolean}
      */
  showEmpty: function showEmpty() {
    return false;
  },

  /**
      * Determines if we need to show the current data regardless if it belongs to data or not
      * Can be overridden to change behaviour
      * @returns {boolean}
      */
  showCurrentData: function showCurrentData() {
    return false;
  },

  /**
      * Adds custom parameters to process the template
      * @returns {{vertical: boolean}}
      */
  getTemplateParams: function getTemplateParams() {
    var self = this;
    var properties = self.properties;

    return {
      vertical: !properties.horizontal,
      horizontal: properties.horizontal };

  },

  /**
      * Cleans current data
      */
  cleanData: function cleanData() {
    var self = this;
    var value = { id: "", label: "" };
    var selectedItem = self.element.find(".ui-radio-state-checked");

    selectedItem.removeClass("ui-radio-state-checked");
    self.setDisplayValue(value);
    self.setValue(value, false);
    self.clearDisplayValue();
  },

  /**
      * Check value of control is changed
      * @returns {boolean}
      */
  controlValueIsChanged: function controlValueIsChanged() {
    var self = this;
    var properties = self.properties;

    var value = self.getValue();
    var compareValue = properties.originalValue;
    var baseValue = properties.defaultValue;

    var result = true;

    if (value && value.length !== undefined) {
      value = value[0];
    }
    if (compareValue && compareValue.length !== undefined) {
      compareValue = compareValue[0];
    }
    if (baseValue && baseValue.length !== undefined) {
      baseValue = baseValue[0];
    }

    value = value && value.id !== undefined ? value.id : value;
    compareValue = compareValue && compareValue.id !== undefined ? compareValue.id : compareValue;
    baseValue = baseValue && baseValue.id !== undefined ? baseValue.id : baseValue;

    if (baseValue !== undefined) {
      if (baseValue == value && value == compareValue) {
        result = false;
      } else {
        if ((value === null || value === "") && (compareValue === null || compareValue === "")) {
          result = false;
        } else {
          result = compareValue == value ? false : true;
        }
      }
    } else {
      if ((value === null || value === "") && (compareValue === null || compareValue === "")) {
        result = false;
      } else {
        result = compareValue == value ? false : true;
      }
    }

    return result;
  },
  /**
      * Clean html because some scenarios render twice
      */
  emptyControlByAsyncRenderDeferred: function emptyControlByAsyncRenderDeferred() {
    var control = this.getControl();
    control.empty();
  } });