/*
*   Name: Bizagi Device Render Factory
*   Author: UI Forms Team
*   Comments:
*   -   This script will detect the current device and then will load the appropriate factory on demand
*/

$.Class.extend("bizagi.rendering.device.factory", {}, {

  /**
                                                         * Constructor, detects the device
                                                         * @param params
                                                         */
  init: function init(params) {
    this.execution_device = bizagi.util.detectDevice();
    this.cachedFactory = {};
  },

  /**
      * Returns the appropriate render factory based on the device detection
      * Because it loads the scripts on demand it must return a promise
      * @param dataService
      * @param device
      * @returns {*}
      */
  getRenderFactory: function getRenderFactory(dataService, device) {
    var self = this;
    device = device || "desktop";
    var defer = new $.Deferred();

    if (typeof self.cachedFactory[device] !== "undefined") {
      // Reset print version value
      self.cachedFactory[device].printVersion = false;
      // Resolve with cached factory                
      defer.resolve(self.cachedFactory[device]);
    } else {
      // If factory is not cached then build it
      if (device === "print") {

        // Load print version references
        self.cachedFactory[device] = new bizagi.rendering.print.factory(dataService);

        // Wait for async loading
        self.cachedFactory[device].initAsyncStuff().
        done(function () {
          // Resolve operation
          defer.resolve(self.cachedFactory[device]);
        });

      } else if (device === "desktop") {

        // Load desktop references
        self.cachedFactory[device] = new bizagi.rendering.desktop.factory(dataService);

        // Wait for async loading
        self.cachedFactory[device].initAsyncStuff().
        done(function () {
          // Resolve operation
          defer.resolve(self.cachedFactory[device]);
        });

      } else if (bizagi.util.isTabletDevice(device)) {

        // Load tablet references
        self.cachedFactory[device] = new bizagi.rendering.tablet.factory(dataService);

        // Wait for async loading
        self.cachedFactory[device].initAsyncStuff().
        done(function () {
          // Resolve operation
          defer.resolve(self.cachedFactory[device]);
        });
      } else if (bizagi.util.isSmartphoneDevice(device)) {

        // Load tablet references
        self.cachedFactory[device] = new bizagi.rendering.smartphone.factory(dataService);

        // Wait for async loading
        self.cachedFactory[device].initAsyncStuff().
        done(function () {

          // Resolve operation
          defer.resolve(self.cachedFactory[device]);
        });
      } else {
        // if no device suuported
        bizagi.showMessageBox("Not supported device: " + device);
        defer.reject("Not supported device: " + device);
      }
    }

    return defer.promise();
  },

  /**
      * Returns the current data service being used
      * @returns {*}
      */
  getDataService: function getDataService() {
    return this.dataService;
  } });