  controlValueIsChanged: function controlValueIsChanged() {
    var self = this;
    var properties = self.properties;
    var value = self.getValue();
    var compareValue = properties.originalValue;
    var baseValue = properties.defaultValue;
    var result = true;
    var renderType = bizagi.util.normalizeType(properties.type);
    var form = self.getFormContainer();

    //To force to send value when control's retype is duplicate, fixed to start forms.
    if (properties.retype === "duplicate" && self.cloneRender && form.isStartForm()) return true;

    // Flag to force to collect data
    if ($.forceCollectData) {
      return true;
    }

    // Verify if control has been ready
    if (renderType === "grid" && self.ready().state() === "pending") {
      return false;
    }

    if (["combo", "queryCombo", "list", "queryList", "radio", "queryRadio"].includes(renderType) || /^cascadingcombo$/i.test(renderType)) {
      if (value && value.length !== undefined) {
        value = value[0];
      }
      if (compareValue && compareValue.length !== undefined) {
        compareValue = compareValue[0];
      }
      if (baseValue && baseValue.length !== undefined) {
        baseValue = baseValue[0];
      }
      value = value && value.id ? value.id : value;
      compareValue = compareValue && compareValue.id ? compareValue.id : compareValue;
      baseValue = baseValue && baseValue.id ? baseValue.id : baseValue;
      if (typeof baseValue != "undefined" && baseValue != null) {
        result = compareValue == value && compareValue != baseValue ? false : true;
      } else {
        result = compareValue == value ? false : true;
      }
    } else if (renderType === "boolean" || renderType === "queryBoolean" || renderType === "queryCheck") {
      result = bizagi.util.parseBoolean(compareValue) == bizagi.util.parseBoolean(value) ? false : true;
    } else if (renderType === "searchNumber") {
      result = !!value;
    } else if (renderType === "number") {
      if ((value === null || value === "") && (compareValue === null || compareValue === "")) {
        result = false;
      } else {
        result = compareValue === value ? false : true;
      }
    } else if (renderType === "text") {
      if ((value === null || value === "") && (compareValue === null || compareValue === "")) {
        result = false;
      } else {
        result = compareValue === value ? false : true;
      }
    } else if (renderType === "date") {
      if (!compareValue && !value) {
        result = false;
      } else {
        if (!properties.showTime) {
          value = bizagi.util.getNormalizedDate(value, properties.fullFormat);
          compareValue = bizagi.util.getNormalizedDate(compareValue, properties.fullFormat);
        }

        result = compareValue == value ? false : true;
      }
    } else if (renderType === "actionLauncher") {
      //Object must be an array
      result = false;
      var compareValueObj = [];
      try {
        if (typeof compareValue === "string") {
          compareValueObj = JSON.parse(compareValue);
        } else {
          compareValueObj = compareValue;
        }
      } catch (e) {
      }

      var _findAction = function _findAction(guid, targetObj) {
        guid = guid || "";
        targetObj = targetObj || [];
        var result = false;
        for (var i = 0, l = targetObj.length; i < l; i++) {
          if (targetObj[i].guidAction == guid) {
            result = true;
            break;
          }
        }
        return result;
      };

      if (compareValueObj.length == value.length) {
        for (var i = 0, l = compareValueObj.length; i < l; i++) {
          if (!_findAction(compareValueObj[i].guidAction, value)) {
            result = true;
          }
        }

      } else {
        result = true;
      }
    } else if (renderType === "polymorphicLauncher" || renderType === "entityTemplate") {
      //Object must be an array
      if (compareValue.length != value.length) {
        result = true;
      } else {
        result = JSON.encode(value) == JSON.encode(compareValue) ? false : true;
      }
    } else if (renderType === "searchList") {
      var objectValue = {};
      try {
        objectValue = JSON.parse(value);
      } catch (e) {
        console.log("Error when detect change value on searchList control");
      }
      result = JSON.encode(compareValue) == JSON.encode(objectValue.value) ? false : true;
    } else if (["signature", "image"].includes(renderType)) {
      // When a image/signature is uploaded or removed from a Form,
      // the change is saved automatically to the activity's scope
      result = false;
    } else {
      result = compareValue === value ? false : true;
    }

    return result;
  },

