  /* Customizes cell color*/
  changeCellColor: function changeCellColor(color) {
    var self = this;
    var control = self.getControl();
    if (control) {
      if (color != 'none') {
        control.css("color", color);
        control.find("input, textarea, label").each(function (i, element) {
          $(element).css("color", color);
        });
      } else {
        control.css("color", "");
        control.find("input, textarea, label").each(function (i, element) {
          $(element).css("color", "");
        });
      }
    }
  },

  /**
      * Customizes cell background color
      * @param color
      */
  changeCellBackgroundColor: function changeCellBackgroundColor(color) {
    var self = this;
    var control = self.getControl();
    var backgroundColor = "";
    var backgroundImage = "";

    if (control) {
      if (color != 'none') {
        backgroundColor = color;
        backgroundImage = "none";
      }

      if (self.properties.editable) {
        var needBGPaint = false;

        control.find("input, textarea, .ui-selectmenu").each(function (i, element) {

          if ((element.type == "text" || element.type == "textarea") && !$(element).hasClass('ui-select-data'))
          needBGPaint = true;

          $(element).css({
            "background-color": backgroundColor,
            "background-image": backgroundImage });

        });

        if (!needBGPaint) {
          control.closest("td").each(function (i, element) {
            $(element).css({
              "background-color": backgroundColor,
              "background-image": backgroundImage });

          });
        }

      } else {
        control.each(function (i, element) {
          //TODO: Validar si se aplican los estilos en tablet aun sin esta linea, cuidado con desktop
          var item = $(element).hasClass("ui-bizagi-cell-readonly") ? control.closest("td") : $(element);
          item.css({
            "background-color": backgroundColor,
            "background-image": backgroundImage });

        });
      }
    }


  },

  /**
      * Changes the cell visibility
      * @param argument
      */
  changeCellVisibility: function changeCellVisibility(argument) {
    var self = this;
    var properties = self.properties;
    var control = self.getControl();

    if (control) {
      // Hide - show the render
      if (bizagi.util.parseBoolean(argument) == true) {
        //control.show();
        control.css("visibility", "visible");
        control.css("display", "");

        self.element && self.element.show();
      } else {
        //control.hide();
        control.css("visibility", "hidden");
      }
    }

    // Update properties
    properties.visible = argument;
  },

  /**
      * Changes the cell required
      * @param argument
      */
  changeCellRequired: function changeCellRequired(argument) {
    var self = this;
    var properties = self.properties;
    var control = self.getControl();

    if (control) {
      // Check if argument is true and control donot have any value
      if (bizagi.util.parseBoolean(argument) == true && bizagi.util.isEmpty(self.value)) {
        control.addClass('control-required');
        control.append('<div class="ui-bizagi-render-control-required"></div>');
      } else {
        control.removeClass('control-required');
        $(".ui-bizagi-render-control-required", control).remove();
      }
      // Update properties
      properties.required = argument;
    }
  },

