  /**
      * Provides a way to asynchronously observe changes in the intersection
      * of a target element with an ancestor element
      * @param $context
      * @param callbackFn
      */
  setInterceptionObserver: function setInterceptionObserver($context, callbackFn) {
    var self = this;

    var intersectionObserver = new IntersectionObserver(function () {
      if (!self.pluginInitialized && $context.width() !== 0) {

        // Execute callback
        if (typeof callbackFn === "function") {
          callbackFn();
        }

        // Close
        intersectionObserver.disconnect();
      }
    });

    intersectionObserver.POLL_INTERVAL = 100; // Time in milliseconds.
    intersectionObserver.observe($context.get(0));
  },

  /**
      * Provides a way to asynchronously observe changes in the resize
      * of a target element
      * @param $context
      * @param callbackFn
      */
  setResizeObserver: function setResizeObserver($context, callbackFn) {
    var self = this;

    var resizeObserver = new ResizeObserver(function () {
      if (!self.pluginInitialized && $context.width() !== 0) {

        // Execute callback
        if (typeof callbackFn === "function") {
          callbackFn();
        }

        // Close
        resizeObserver.disconnect();
      }
    });
    resizeObserver.observe($context.get(0));
  },

  /**
      * Restore appearance of grid
      */
  restoreGridAppearance: function restoreGridAppearance() {
    var self = this;
    var indexes = self.getIndexes();

    self.restoreAppearance();

    if (self.columns.length > 0) {
      $.each(self.columns, function (j, column) {
        for (var i = 0; i < indexes.length; i++) {
          column.decorateRenders[indexes[i]].restoreCellAppearance();
        }
      });
    }
  },

  /**
      * Save visual changes of children in grid
      */
  saveGridChildrenVisualChanges: function saveGridChildrenVisualChanges() {
    var self = this;
    var indexes = self.getIndexes();

    self.gridChildrenVisualChanges = [];

    if (self.columns.length > 0) {
      $.each(self.columns, function (j, column) {
        if (column.properties.xpath) {
          for (var i = 0; i < indexes.length; i++) {
            var cell = column.decorateRenders[indexes[i]];

            if (cell && cell.properties)
            self.gridChildrenVisualChanges[cell.properties.id + indexes[i]] = cell.visualChanges;
          }

        }
      });
    }
  },

  /**
      * Restore visual changes of children in grid
      */
  restoreGridChildrenVisualChanges: function restoreGridChildrenVisualChanges() {
    var self = this;
    var indexes = self.getIndexes();
    if (self.columns.length > 0) {
      $.each(self.columns, function (j, column) {
        if (column.properties.xpath) {
          for (var i = 0; i < indexes.length; i++) {
            var cell = column.decorateRenders[indexes[i]];

            if (cell && cell.properties)
            cell.visualChanges = self.gridChildrenVisualChanges[cell.properties.id + indexes[i]] || {};
          }

        }
      });
    }

    delete self.gridChildrenVisualChanges;
  },

  /**
      * Restore appearance of cell
      */
  restoreCellAppearance: function restoreCellAppearance() {
    var self = this;

    for (var command in self.visualChanges) {
      switch (command) {
        case "color":
          self.changeCellColor(self.visualChanges[command]);
          break;
        case "backgroundColor":
          self.changeCellBackgroundColor(self.visualChanges[command]);
          break;
        case "required":
          self.changeCellRequired(self.visualChanges[command]);
          break;
        case "editability":
          self.changeEditabilityCellControl(self.visualChanges[command], self.surrogateKey, self.properties.xpath);
          break;
        case "visibility":
          self.changeCellVisibility(self.visualChanges[command]);
          break;
        case "minValue":
          self.changeMinValue(self.visualChanges[command]);
          break;
        case "maxValue":
          self.changeMaxValue(self.visualChanges[command]);
          break;}

    }
  },

