        button.nextWithoutValidations ? "nextWithoutValidations" :
        button.submitData ? "save" :
        button.cancel ? "cancel" :
        button.back ? "back" :
        button.createnewcase ? "createnewcase" :
        button.validate ? "validate" : "";

