  /**
      * This function execute action to save all data of render, but
      * the server service dont execute any rule on the server
      */
  submitData: function submitData(args) {
    var self = this;

    // Avoid the execution of server actions for mobile offline
    if (bizagi.util.isNativeMobileDevice() && !this.allowOnlineActions())
    return;

    // Collect data
    var data = self.collectRenderValuesForSubmit();

    // Check is offline form        
    var isOfflineForm = bizagi.util.isOfflineForm({ context: self });
    if (isOfflineForm && self.properties && !self.properties.idPageCache) {
      self.properties.idPageCache = "";
    }

    // Mark data collected as original values
    $.each(data, function (key, value) {
      var renders = self.getRenders(key);
      $.each(renders, function (i, render) {
        render.updateOriginalValue();
      });
    });

    // Don't do anything if there is no data
    if ($.isEmptyObject(data)) {
      self.isOfflineRefreshWithEdition = false;
      return;
    }

    var waitingDeferreds = [];
    var deferredsToWait = null;
    var executeSubmitData = null;

    if (data && data.deferreds) {
      deferredsToWait = $.when.apply($, data.deferreds);
      waitingDeferreds.push(deferredsToWait);
      delete data.deferreds;
    }

    // Call the service
    if (isOfflineForm) {
      executeSubmitData = self.dataService.submitData({
        action: self.isRefreshingAfterSync ? "submitdata" : "save",
        data: data,
        xpathContext: self.properties.xpathContext,
        contexttype: self.properties.contexttype || "",
        isOfflineForm: isOfflineForm,
        offlineType: self.params.offlineType,
        idCase: self.params.idCase,
        idWorkitem: self.params.idWorkitem });

    } else {
      executeSubmitData = self.dataService.multiaction().submitData({
        action: "SUBMITDATA",
        data: data,
        idPageCache: self.properties.idPageCache,
        xpathContext: self.properties.xpathContext,
        contexttype: self.properties.contexttype || "" });

    }

    return $.when.apply($, waitingDeferreds).then(function () {
      return $.when(executeSubmitData).fail(function (message) {
        message = self.processFailMessage(message);
      });
    });
  },

  /**
      * This function send a request to the server to execute a rule
      */
  executeRule: function executeRule(args) {
    var self = this;
    var properties = self.properties;
    var xpathContext = args.xpathContext;
    var deferred = $.Deferred();

    // Concat inheritance context
    if (properties.xpathContext !== "") {
      xpathContext = xpathContext !== "" ? properties.xpathContext + "." + xpathContext : properties.xpathContext;
    }

    // Perform the service
    $.when(self.dataService.multiaction().executeRule({
      idPageCache: properties.idPageCache,
      rule: args.rule,
      xpathContext: xpathContext,
      contexttype: self.properties.contexttype || "" })).
    done(function (message) {
      var type = message && message.type ? message.type : "";
      var form = self.getFormContainer();

      // Check for validation messages
      self.getFormContainer().clearValidationMessages({ clearOnlyServer: true });

      if (["validationMessages", "alertMessages"].includes(type)) {
        if (type === "alertMessages") {
          bizagi.modalError && bizagi.modalError.show(message.messages.join("\n\n"), 'workportal-general-word-information', false);
        } else {
          form.addValidationMessage.apply(self, [message.messages, undefined, undefined, undefined, false, "serverMessage"]);
        }

        deferred.reject(message);
      } else {
        deferred.resolve(message);
      }
    }).fail(function (message) {
      message = self.processFailMessage(message);
      deferred.reject(message);
    });

    return deferred.promise();
  },

