  /**
      * Add handlers to network management
      */
  configureNetworkHandlers: function configureNetworkHandlers() {
    var self = this;
    self.online = bizagi.util.isConnected();
    if (self.isOfflineForm && bizagi.util.isMobileDevice() && ("" + BIZAGI_ENVIRONMENT).toLowerCase() === "debug") {
      self.online = self.getParams().online;
      console.log("CONNECTED: ", self.online);
    }

    // Update the online status based on connectivity
    if (bizagi.util.isNativeMobileDevice() || bizagi.util.isMobileDevice() && ("" + BIZAGI_ENVIRONMENT).toLowerCase() === "debug") {
      $(document).off("online.form");
      $(document).on("online.form", this.handleConnectionChange.bind(self));

      $(document).off("offline.form");
      $(document).on("offline.form", this.handleConnectionChange.bind(self));

      //Connection gained with form opened
      $(document).off("offline-notify-form-sync-event");
      $(document).on("offline-notify-form-sync-event", this.handleOfflineSyncEvent.bind(self));
    }
  },

  /**
      * Event handler
      * @param event
      */
  handleConnectionChange: function handleConnectionChange(event) {
    var form = this.getFormContainer();
    if (bizagi.util.isMobileDevice() && ("" + BIZAGI_ENVIRONMENT).toLowerCase() === "debug") {
      this.online = event.type === "online";
    } else {
      this.online = bizagi.util.isConnected();
    }

    // You lost connection.
    if (event.type === "offline") {
      form.triggerHandler("offlineForm");
    }

    // You are now back online.
    if (event.type === "online") {
      form.triggerHandler("onlineForm");
    }
  },

  /**
      * Push successful hanlder, refresh form and copy current work
      */
  handleOfflineSyncEvent: function handleOfflineSyncEvent(event, params) {
    var self = this;
    var formParams = self.params || {};
    if (formParams.hasSyncError) {
      return;
    }
    var offlineFormId = formParams.idCase + "-" + formParams.idWorkitem;
    switch (params.type) {
      case "download-form-success":
        /**
                                     * 1. Store current data in offline submit
                                     * 2. Refresh form
                                     * 3. Merge data using offline isEdited method
                                     */
        var paramsId = params.idCase + "-" + params.idWorkitem;
        if (offlineFormId === paramsId) {
          console.log("Form downloaded successfully. It will be refreshed", params);

          $.when(self.dataService.database.validateLocalDataForTaskExists(paramsId).then(function (dataResult) {
            if (dataResult === true) {
              self.isOfflineRefreshWithEdition = true;
            }

            $.when(self.refreshForm(null, false)).then(function () {
              applySyncLoader(false);
            });
          }));
        }
        break;
      case "push-tasks-started":
        if (params.tasks != undefined && params.tasks[offlineFormId]) {
          applySyncLoader(true);
        }
        break;
      case "push-tasks-error":
        applySyncLoader(false);
        if (self.validationController != undefined) {
          self.validationController.clearValidationMessages();
          self.validationController.showErrorMessage(params.message);
        }
        break;
      case "fetch-tasks-started":
        if (params.tasks != undefined && params.tasks[offlineFormId]) {
          applySyncLoader(true);
        }
        break;
      case "download-finished":{
          applySyncLoader(false);
          break;
        }
      case "download-error":
        applySyncLoader(false);
        break;
      default:
        console.warn("Event hast not implementation in bizagi.rendering.form - handleOfflineSyncEvent", params.type, params);
        break;}


    function applySyncLoader(isStart) {
      var loaders = 3;
      var proceed = false;
      if (isStart) {
        proceed = !(self.isSyncLoaderRunning === true);
      } else
      {
        proceed = self.isSyncLoaderRunning === true;
      }
      if (proceed) {
        self.isSyncLoaderRunning = isStart;
        for (var loaderIndex = 0; loaderIndex < loaders; loaderIndex++) {
          if (isStart) {
            bizagi.loading.start();
          } else
          {
            bizagi.loading.stop();
          }
        }
      }
    }
  },

