/**
 *   Name: Bizagi Render Form Link Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for form link renders
 */

$.Class.extend("bizagi.rendering.formLink", {}, {

  /**
                                                   * Update or init the element data
                                                   * @param data
                                                   */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Set properties
    var properties = this.properties;

    properties.displayType = "value";
    properties.maximized = bizagi.util.parseBoolean(this.properties.maximized) != null ? bizagi.util.parseBoolean(this.properties.maximized) : false;

    // Calculate layout properties
    this.calculateInitialLayoutProperties();
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {*|jQuery}
      */
  renderControl: function renderControl() {
    var self = this;
    var properties = self.properties;
    var template = self.renderFactory.getTemplate("formLink");

    // Render template
    return $.fasttmpl(template, {
      id: properties.id,
      align: properties.valueAlign,
      displayName: properties.displayName,
      value: properties.value,
      editable: properties.editable,
      likeButton: bizagi.util.parseBoolean(properties.likeButton) || false });

  },

  /**
      * Method to render non editable values
      * @returns {*|*|jQuery}
      */
  renderReadOnly: function renderReadOnly() {
    // Executes the same template than normal render
    return this.renderControl();

  },

  /**
      * Method to customize each render after processed
      */
  postRender: function postRender() {
    this._super();
    this.getLabel().addClass("bz-form-label--hidden");
  },

  /**
      * Method to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    this._super();
    this.getLabel().addClass("bz-form-label--hidden");
  },

  /**
      * Submits a edit request for a link display form
      * @returns {*} Returns a deferred
      */
  submitEditRequest: function submitEditRequest() {
    var self = this;
    var properties = self.properties;

    // Don't send request when the link is not editable
    if (!properties.editable && !properties.value) return null;

    return self.dataService.editLinkForm({
      url: properties.editUrl,
      idRender: properties.id,
      xpath: self.getFormLinkXpath(),
      xpathContext: properties.xpathContext,
      idPageCache: properties.idPageCache });

  },

  /**
      * Submits a link save request for the displayed form
      * @param data
      * @returns {*} Returns a deferred
      */
  submitSaveRequest: function submitSaveRequest(data) {
    var self = this;
    var properties = self.properties;

    // Don't send request when the link is not editable
    if (!properties.editable && !properties.value) return null;

    var xpath = self.getFormLinkXpath();
    var xpathContext = xpath;

    if (typeof properties.xpathContext !== "undefined" && properties.xpathContext !== "") {
      xpathContext = xpath ? properties.xpathContext + "." + xpath : properties.xpathContext;
    }

    return self.dataService.saveLinkForm({
      url: properties.saveUrl,
      idRender: properties.id,
      xpathContext: xpathContext,
      submitData: data }).
    done(function (response) {
      var executeSubmitOnChange = true;

      if (response && response.type === "validationMessages")
      executeSubmitOnChange = false;

      if (executeSubmitOnChange)
      self.submitOnChange({});
    });
  },

  /**
      * Sends all the data to the server then refreshes the form
      * @param data
      * @returns {*} Returns a promise
      */
  saveFormLinkData: function saveFormLinkData(data) {
    var self = this;
    var properties = self.properties;

    var xpath = self.getFormLinkXpath();
    var xpathContext = xpath;

    if (typeof properties.xpathContext !== "undefined" && properties.xpathContext !== "") {
      xpathContext = xpath ? properties.xpathContext + "." + xpath : properties.xpathContext;
    }

    var submitData = $.extend({}, data);
    delete submitData.idPageCache;

    return $.when(!bizagi.util.isEmpty(submitData) ? self.dataService.multiaction().submitData({
      action: "SAVE",
      data: submitData,
      idPageCache: data.idPageCache,
      xpathContext: xpathContext }) :
    undefined);
  },

  /**
      * Submits a rollback request
      * @param params
      * @returns {*} Returns a deferred when done
      */
  submitRollbackRequest: function submitRollbackRequest(params) {
    var self = this;
    var properties = self.properties;

    return $.when(function () {
      // Don't send request when the link is not editable
      if (!properties.editable && !properties.value) {
        return null;
      } else {
        return self.dataService.rollbackLinkForm({
          url: properties.rollbackUrl,
          idRender: properties.id,
          xpath: self.getFormLinkXpath(),
          xpathContext: properties.xpathContext,
          idPageCache: params ? params.idPageCache : properties.idPageCache });

      }
    }());
  },

  /**
      * Submits a commit request
      * @returns {*} Returns a deferred when done
      */
  submitCommitRequest: function submitCommitRequest() {
    var self = this;
    var properties = self.properties;

    return $.when(function () {
      // Don't send request when the link is not editable
      if (!properties.editable && !properties.value) {
        return null;
      } else {
        return self.dataService.commitLinkForm({
          url: properties.commitUrl,
          idRender: properties.id,
          xpath: self.getFormLinkXpath(),
          xpathContext: properties.xpathContext,
          idPageCache: properties.idPageCache });

      }
    }());
  },

  /**
      * Submits a checkpoint request
      * @returns {*} Returns a deferred when done
      */
  submitCheckpointRequest: function submitCheckpointRequest() {
    var self = this;
    var properties = self.properties;

    return $.when(function () {
      // Don't send request when the link is not editable
      if (!properties.editable && !properties.value) {
        return null;
      } else {
        return self.dataService.sendCheckpoint({
          url: properties.checkpointUrl,
          idRender: properties.id,
          xpath: self.getFormLinkXpath(),
          xpathContext: properties.xpathContext,
          idPageCache: properties.idPageCache });

      }
    }());
  },

  /**
      * Submits a submitData request
      * @returns {*} Returns a deferred when done
      */
  submitDataRequest: function submitDataRequest() {
    var self = this;
    var properties = self.properties;

    return $.when(function () {
      // Don't send request when the link is not editable
      if (!properties.editable && !properties.value) {
        return null;
      } else {
        return self.dataService.submitData({
          url: properties.checkpointUrl,
          action: "SUBMITDATA",
          idRender: properties.id,
          xpath: self.getFormLinkXpath(),
          xpathContext: properties.xpathContext,
          idPageCache: properties.idPageCache });

      }
    }());
  },

  /**
      * Public method to determine if a value is valid or not
      * @param invalidElements
      */
  isValid: function isValid(invalidElements) {
    // Automatically this render is valid, so we can't check a required link
  },

  /**
      * Get Form Link xpath to use
      * @returns {*}
      */
  getFormLinkXpath: function getFormLinkXpath() {
    return this.properties.xpath;
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @returns {boolean}
      */
  canBeSent: function canBeSent() {
    return false;
  },

  /**
      * Sets the value in the rendered control
      * @param value
      */
  setDisplayValue: function setDisplayValue(value) {
    // Don't do anything
  },

  /*
     * Cleans current value
     */
  cleanData: function cleanData() {
    // Don't do anything
  } });