/**
 *   Name: Bizagi Render Document Generator
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for document generator render
 */

$.Class.extend("bizagi.rendering.document", {}, {

  /**
                                                   * Update or init the element data
                                                   * @param data
                                                   */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    var properties = this.properties;

    properties.validate = bizagi.util.parseBoolean(properties.validate) != null ? bizagi.util.parseBoolean(properties.validate) : false;
    properties.downloadalldocuments = properties.downloadalldocuments ? properties.downloadalldocuments : false;
    properties.allowGenerate = properties.allowGenerate !== undefined ? properties.allowGenerate : true;
    properties.append = properties.append ? properties.append : false;

    if (properties.caption) {
      properties.caption = $.trim(properties.caption);

      if (properties.caption.length === 0) {
        properties.caption = this.getResource("render-documentgenerator-button-label");
      }
    } else {
      properties.caption = this.getResource("render-documentgenerator-button-label");
    }
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {*}
      */
  renderControl: function renderControl() {
    var self = this;

    self.preRenderControl();

    var properties = self.properties;
    var mode = self.getMode();
    var downloadAllDocuments = properties.downloadalldocuments;
    var allowGenerate = properties.allowGenerate;
    var template = self.renderFactory.getTemplate(self.getTemplate());
    var documents = properties.value || [];
    var noFiles = documents ? documents.length === 0 : true;

    // Render template
    var html = self.getControlHtml(template, mode, downloadAllDocuments, allowGenerate, properties, noFiles);

    // Render current children
    var items = this.renderDocumentsHtml(documents);
    items += this.renderActionsHtml(documents, noFiles, downloadAllDocuments);

    html = self.replaceFilesHtml(html, items);
    return html;
  },

  /**
      * Template method to implement in each children to customize each control
      * @param template
      * @param mode
      * @param downloadAllDocuments
      * @param allowGenerate
      * @param properties
      * @param noFiles
      * @returns {*|jQuery}
      */
  getControlHtml: function getControlHtml(template, mode, downloadAllDocuments, allowGenerate, properties, noFiles) {
    var self = this;

    return $.fasttmpl(template, {
      noActivateButton: false,
      editable: mode !== "execution",
      mode: mode,
      downloadalldocuments: downloadAllDocuments,
      required: properties.required,
      allowgenerate: allowGenerate,
      xpath: self.getXpath(properties.xpath),
      append: properties.append,
      caption: properties.caption,
      noFiles: noFiles,
      allowSendInMail: properties.allowSendInMail });

  },

  /**
      * Template method to implement in each children to customize each control
      * @param xpath
      * @returns {*}
      */
  getXpath: function getXpath(xpath) {
    return xpath;
  },

  /**
      * Template method to implement in each children to customize each control
      */
  preRenderControl: function preRenderControl() {
  },

  /**
      * Template method to implement in each children to customize each control
      * @returns {boolean}
      */
  activateButton: function activateButton() {
    return true;
  },

  /**
      * Returns the xpath to be used
      * @param xpath
      * @returns {*}
      */
  getDocumentXpath: function getDocumentXpath(xpath) {
    return xpath;
  },

  /**
      * Returns the xpath to be used
      * @returns {*}
      */
  getContextXpath: function getContextXpath() {
    return this.properties.xpathContext;
  },

  /**
      * Renders a single document item
      * @param file
      * @returns {*|jQuery}
      */
  renderDocumentItem: function renderDocumentItem(file) {
    var self = this;
    var mode = self.getMode();
    var template = self.renderFactory.getTemplate(self.getTemplateItem());
    var url = self.buildItemUrl(file);

    // Don't render urls when not running in execution mode
    if (mode !== "execution") url = "javascript:void(0);";

    var html = $.fasttmpl(template, {
      url: url,
      allowDelete: self.properties.allowDelete,
      filename: file.fileName,
      id: file.id,
      mode: mode,
      editable: self.properties.editable,
      joined: false });


    return html;
  },

  /**
      * Builds the upload item url
      * @param file
      * @returns {*}
      */
  buildItemUrl: function buildItemUrl(file) {
    var self = this;
    var properties = self.properties;

    return self.dataService.getUploadFileUrl({
      idRender: properties.id,
      xpath: self.getDocumentXpath(properties.xpath),
      xpathContext: self.getContextXpath(),
      idPageCache: properties.idPageCache,
      fileId: file.id,
      sessionId: self.getSessionId() });

  },

  /**
      * Builds the download all items item url
      * @returns {*}
      */
  buildAllDocumentsUrl: function buildAllDocumentsUrl() {
    var self = this;
    var properties = self.properties;

    return self.dataService.getAllDocumentsDownloadUrl({
      xpath: self.getDocumentXpath(properties.xpath),
      xpathContext: self.getContextXpath(),
      idPageCache: properties.idPageCache,
      idCase: properties.caseId,
      idWorkItem: properties.workitemId,
      sessionId: self.getSessionId() });

  },

  /**
      * Template method to implement in each device to customize each render after processed (Virtual method)
      * @returns {string}
      */
  postRender: function postRender() {
    // Call base
    this._super();

    return "<div></div>";
  },

  /**
      * Template method to implement in each device to customize each render after processed in read-only mode
      */
  postRenderReadOnly: function postRenderReadOnly() {
    var self = this;
    var mode = self.getMode();

    if (mode !== "design") {
      var control = self.getControl();
      var htmlControl = self.renderControl();

      control.append(htmlControl);

      self.updateControlForPrintMode(control);

      // Execute the same as post-render
      self.postRender();
    }
  },

  /**
      * Saves the form before the document generation
      * @returns {*}
      */
  validateContainer: function validateContainer() {
    // Check if the form is valid, then generate
    var form = this.getFormContainer();
    return form.validateForm();
  },

  /**
      * Saves the form before the document generation
      * @returns {*}
      */
  save: function save() {
    var form = this.getFormContainer();

    return form.saveForm();
  },

  /**
      * Replaces a {{files}} tag in the container for the specified "replace" element
      * @param html
      * @param replace
      * @returns {*}
      */
  replaceFilesHtml: function replaceFilesHtml(html, replace) {
    return html.replace("{{files}}", replace);
  },

  /**
      * Replaces a {{actions}} tag in the container for the specified "replace" element
      * @param html
      * @param replace
      * @returns {*}
      */
  replaceActionsHtml: function replaceActionsHtml(html, replace) {
    return html.replace("{{actions}}", replace);
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @returns {boolean}
      */
  canBeSent: function canBeSent() {
    // This render cannot be sent because it is full ajax
    return false;
  },

  /**
      * Method to determine if the render value can be sent to the server or not
      * @param invalidElements
      * @returns {boolean}
      */
  isValid: function isValid(invalidElements) {
    var self = this;
    var properties = self.properties;
    var message;

    // Don't check non-editable renders
    if (bizagi.util.parseBoolean(properties.visible) === false) return true;

    // Don't check non-editable renders
    if (bizagi.util.parseBoolean(properties.editable) === false) return true;

    // Clear error message
    self.setValidationMessage("");

    // Check required
    if (properties.required && !properties.buttonExecuted) {
      if (self.value) {
        if (self.value.length > 0) return true;
        message = self.getResource("render-document-required-text");
        invalidElements.push({ xpath: properties.xpath, message: message });
        return false;
      }
    }

    return true;
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    var self = this;

    // Call base
    self._super();

    // Attach handler
    var $generateLink = $(".ui-bizagi-button-documentgenerator", self.getControl());

    if (self.isExecutionMode()) {
      $generateLink.click(function (e) {
        if (e.isPropagationStopped()) return;
        e.stopPropagation();

        self.properties.buttonExecuted = true;

        if (self.properties.validate) {
          // Check if the form is valid, then generate
          var isValid = self.validateContainer();
          if (isValid) {
            self.documentTemplateGenerate();
          }
        } else {
          // Just generate the form
          self.documentTemplateGenerate();
        }

        self.properties.buttonExecuted = false;
      });

      // Configure basic handlers
      self.attachEvents();
    }
  },

  /**
      * Method to customize the render's behaviour to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    if (this.isExecutionMode()) {
      this.attachEvents();
    }
  },

  /**
      *  Configure event handlers
      */
  attachEvents: function attachEvents() {
    var self = this;
    var $control = self.getControl();

    // Configure download handler for document item
    $(".ui-bizagi-document-upload-item a > span", $control).click(function (e) {
      if (e.isPropagationStopped()) return;
      e.stopPropagation();

      var $item = $(this).closest(".ui-bizagi-document-upload-item");
      var file = $item.data();

      if (!file.joined) {
        self.downloadFile(file);
      } else {
        self.downloadAllFiles({ filename: "TemplatesConcatFilesToPDF.pdf" });
      }
    });

    // Configure preview handler
    $(".ui-bizagi-container-documents-item-delete", $control).click(function (e) {
      if (e.isPropagationStopped()) return;
      e.stopPropagation();

      self.deleteDocumentTemplate($(this).data("filename"));
    });
  },

  /**
      * Creates the event of generation of templates.
      * @param filename
      */
  deleteDocumentTemplate: function deleteDocumentTemplate(filename) {
    var self = this;
    var params = {
      xpath: self.properties.xpath + "[fileName='" + filename + "']",
      idRender: self.properties.id,
      xpathContext: self.getContextXpath(),
      idPageCache: self.properties.idPageCache };


    self.startLoading();

    $.when(self.dataService.deleteUploadFile(params)).
    done(function () {
      $.when(self.dataService.multiaction().getPropertyData({
        xpath: self.properties.xpath,
        idRender: self.properties.id,
        xpathContext: self.getContextXpath(),
        idPageCache: self.properties.idPageCache,
        property: "value" })).
      done(function (documents) {
        self.resolveDeleteDocumentTemplate(documents, false, self.properties.downloadalldocuments);

        // Finish loading
        self.endLoading();
      });
    });
  },

  /**
      * Creates the event of generation of templates.
      */
  documentTemplateGenerate: function documentTemplateGenerate() {
    var self = this;
    var properties = self.properties;

    self.startLoading();

    //Get the context xPath
    properties.xpathcontext = self.getContextXpath();
    properties.xpath = self.getDocumentXpath(properties.xpath);

    self.save().done(function () {

      bizagi.showErrorAlertDialog = false;

      $.when(self.dataService.generateDocumentTemplate(properties)).
      done(function () {

        // Fetch again the value property
        $.when(self.dataService.multiaction().getPropertyData({
          xpath: properties.xpath,
          idRender: properties.id,
          xpathContext: self.getContextXpath(),
          idPageCache: properties.idPageCache,
          property: "value" })).
        done(function (documents) {
          self.resolveDocumentTemplateGenerate(documents, false, properties.downloadalldocuments);

          // Finish loading
          self.endLoading();
        });

      }).fail(function (error) {
        self.endLoading();

        // Add error messages
        self.showErrorMessage(error);
      });
    }).fail(function (message) {
      self.endLoading();

      // Add error messages
      self.showErrorMessage(message);
    });
  },

  /**
      * Show the error message when something went wrong with the service.
      * @param error
      */
  showErrorMessage: function showErrorMessage(error) {
    var self = this;
    var form = self.getFormContainer();
    var messageText = self.resources.getResource('render-upload-alert-maxsize').replace("{0}", BIZAGI_SETTINGS.UploadMaxFileSize);

    try {
      messageText = error && typeof error.responseText !== "undefined" ? JSON.parse(error.responseText.replace(/(\r\n|\n|\r)/gm, "")).message : error;
    } catch (e) {
    }

    form.addErrorMessage(messageText);
    form.endLoading();
  },

  /**
      * Shows the document if the flag is set. (virtual Function)
      */
  autoOpenDocuments: function autoOpenDocuments() {
  },

  /**
      * Template method to implement in each device to customize each render after processed (Virtual method)
      * @param url
      * @param filename
      */
  showDocumentPreview: function showDocumentPreview(url, filename) {
  },

  /**
      * Identifies the extension of the file in a simpler way that ECM does
      * @param file
      * @returns {string}
      */
  returnCssTypeFile: function returnCssTypeFile(file) {

    if (file == undefined || file.lenght === 0) {
      return "ui-bizagi-document-upload-item-generic-file";
    }

    var extension = file.substr(file.lastIndexOf('.') + 1);

    switch (extension) {
      case 'docx':
        return "ui-bizagi-document-upload-item-doc";
      case 'pdf':
        return "ui-bizagi-document-upload-item-pdf";
      case 'xlsx':
        return "ui-bizagi-document-upload-item-xls";
      default:
        return "ui-bizagi-document-upload-item-generic-file";}

  },

  /**
      * Redraw documents when needed
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      */
  redrawDocuments: function redrawDocuments(documents, noFiles, downloadAllDocuments) {
    var self = this;
    var control = self.getControl();
    var html = "";
    var documentContainer = $("[data-component='files']", control);

    // Clear current links
    documentContainer.empty();

    // Draw links again
    html += this.renderDocumentsHtml(documents);

    // Check if there are documents and the flag to download all are available to show the link.
    html += this.renderActionsHtml(documents, noFiles, downloadAllDocuments);

    documentContainer.append(html);

    //Set value
    self.setValue(documents);
  },

  /**
      * This method must be implemented by the specific class of each device. (Virtual method)
      * @param file
      * @returns {*|*|*}
      */
  renderAllDocumentDownload: function renderAllDocumentDownload(file) {
    var self = this;
    var properties = self.properties;
    var mode = self.getMode();

    var template = self.renderFactory.getTemplate(self.getTemplateItem());
    var url = self.buildAllDocumentsUrl();

    // Don't render urls when not running in execution mode
    if (mode !== "execution") url = "javascript:void(0);";

    var html = $.fasttmpl(template, {
      url: url,
      allowDelete: properties.allowDelete,
      filename: file.fileName,
      mode: mode,
      joined: true });


    return self.removeDeleteButton(html);
  },

  /**
      * removeDeleteButton
      * @param html
      * @returns {*}
      */
  removeDeleteButton: function removeDeleteButton(html) {
    var startIndex = html.indexOf("<span class=\"ui-bizagi-container-documents-item-delete");

    if (startIndex > 0) {
      var restOfWord = html.substring(startIndex);
      var finishIndex = restOfWord.indexOf("</span>") + 7;

      return html.substring(0, startIndex) + restOfWord.substring(finishIndex);
    } else {
      return html;
    }
  },

  /**
      * Process request of DeleteDocumentTemplate action
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      */
  resolveDocumentTemplateGenerate: function resolveDocumentTemplateGenerate(documents, noFiles, downloadAllDocuments) {
    // Redraw documents
    this.redrawDocuments(documents, noFiles, downloadAllDocuments);
    this.value = documents;

    // This does not apply for mobile.
    this.autoOpenDocuments();
    this.configureHandlers();
  },

  /**
      * Process request of DocumentTemplateGenerate action
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      */
  resolveDeleteDocumentTemplate: function resolveDeleteDocumentTemplate(documents, noFiles, downloadAllDocuments) {
    // Redraw documents
    this.redrawDocuments(documents, noFiles, downloadAllDocuments);
    this.value = documents;

    // This does not apply for mobile.
    this.configureHandlers();
  },

  /**
      * Method to renders the documents
      * @param documents
      * @returns {string}
      */
  renderDocumentsHtml: function renderDocumentsHtml(documents) {
    var items = "";

    for (var i = 0; i < documents.length; i++) {
      var document = { id: documents[i][0], fileName: documents[i][1] };
      var item = this.renderDocumentItem(document);

      items += item;
    }

    return items;
  },

  /**
      * Methos to renders action of document
      * @param documents
      * @param noFiles
      * @param downloadAllDocuments
      * @returns {string}
      */
  renderActionsHtml: function renderActionsHtml(documents, noFiles, downloadAllDocuments) {
    var actions = "";

    if (downloadAllDocuments && !noFiles && documents.length > 0) {
      var downloadAllDocumentsLink = { fileName: this.getResource("render-document-downloadalldocuments") };
      var action = this.renderAllDocumentDownload(downloadAllDocumentsLink);

      actions += action;
    }

    return actions;
  },

  getTemplate: function getTemplate() {
    return "document";
  },

  getTemplateItem: function getTemplateItem() {
    return "document-item";
  },

  getErrorTemplate: function getErrorTemplate() {
    return "form-error";
  },

  getTemplateDownload: function getTemplateDownload() {
    return "document-download";
  },

  getTemplateReadonly: function getTemplateReadonly() {
    return "document-readonly";
  },

  /**
      * Get blob file
      * @param file
      * @returns {*}
      */
  getDocumentFile: function getDocumentFile(file) {
    var self = this;
    var properties = self.properties;

    return self.dataService.getFile({
      idRender: properties.id,
      xpath: self.getDocumentXpath(properties.xpath),
      xpathContext: self.getContextXpath(),
      idPageCache: properties.idPageCache,
      fileId: file.id,
      contexttype: properties.contexttype }).
    then(function (response) {
      return bizagi.util.handleJSONResponse(response);
    });
  },

  /**
      * Get the download all items item
      * @returns {*}
      */
  getAllDocumentFiles: function getAllDocumentFiles() {
    return this.dataService.getAllDocuments({
      xpath: this.getDocumentXpath(this.properties.xpath),
      xpathContext: this.getContextXpath(),
      idPageCache: this.properties.idPageCache,
      idCase: this.properties.caseId,
      idWorkItem: this.properties.workitemId }).
    then(function (response) {
      return bizagi.util.handleJSONResponse(response);
    });
  },

  /**
      * Download file
      * @param file
      */
  downloadFile: function downloadFile(file) {
    var self = this;

    this.getDocumentFile(file).then(function (blobFile) {
      bizagi.util.downloadBlobFile(blobFile, file.filename);
    }).fail(function (error) {
      var form = self.getFormContainer();
      form && form.failHandler(error);
    });
  },

  /**
      * Download file
      * @param file
      */
  downloadAllFiles: function downloadAllFiles(file) {
    var self = this;

    this.getAllDocumentFiles().then(function (blobFile) {
      bizagi.util.downloadBlobFile(blobFile, file.filename);
    }).fail(function (error) {
      var form = self.getFormContainer();
      form && form.failHandler(error);
    });
  } });