/**
 *   Name: Bizagi Render Fileprint Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for fileprint renders
 */

$.Class.extend("bizagi.rendering.fileprint", {}, {

  /**
                                                    * Update or init the element data
                                                    * @param data
                                                    */
  initializeData: function initializeData(data) {
    // Call base
    this._super(data);

    // Fill default properties
    var properties = this.properties;
    var caption = $.trim(properties.caption);

    properties.caption = !bizagi.util.isEmpty(caption) ? caption : "render-fileprint-property-caption";
    properties.filesCount = properties.filesCount ? properties.filesCount : 0;

    // set default value from normal to value
    properties.displayType = "value";

    // Calculate layout properties
    this.calculateInitialLayoutProperties();

    // A button cannot be required
    properties.required = false;
  },

  /**
      * Method to implement in each children to customize each control
      * @returns {*|jQuery}
      */
  renderControl: function renderControl() {
    var template = this.renderFactory.getTemplate("fileprint");

    return $.fasttmpl(template, {
      caption: this.properties.caption });

  },

  /**
      * Method to render non editable values
      * @returns {*}
      */
  renderReadOnly: function renderReadOnly() {
    // Executes the same template than normal render
    return this.renderControl();
  },

  /**
      * Downloads the file and try to open it
      */
  downloadFile: function downloadFile() {
    return this.dataService.getFilePrint({
      idRender: this.properties.id,
      xpathContext: this.properties.xpathContext || "",
      idPageCache: this.properties.idPageCache,
      contexttype: this.properties.contexttype,
      disposition: "download" }).
    then(function (response) {
      return bizagi.util.handleJSONResponse(response);
    });
  } });