/**
 *   Name: Bizagi Render Query Filter Class
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will define basic stuff for query wrapper
 */

$.Class.extend("bizagi.rendering.queryAdvanced", {}, {

  MAX_TAGS_SUPPORTED: 50,

  OPERATORS: {
    "like": {
      label: "workportal-widget-filter-operators-contains",
      defaultLabel: "Contains",
      appliesTo: ["Text"],
      operator: "APPROX",
      action: "like" },

    "equals": {
      label: "workportal-widget-filter-operators-equals-to",
      appliesTo: ["Text", "Number", "Currency"],
      operator: "EXACT",
      action: "tags" },

    "equal": {
      label: "workportal-widget-filter-operators-equals-to",
      appliesTo: ["DateTime"],
      operator: "EXACT",
      action: "equal" },

    "not-equals": {
      label: "workportal-widget-filter-operators-different-to",
      appliesTo: ["Text", "Number", "Currency"],
      operator: "NOTEXACTORNULL",
      action: "tags" },

    "starts-with": {
      label: "workportal-widget-filter-operators-starts-with",
      defaultLabel: "Starts with",
      appliesTo: ["Text"],
      operator: "WILDCARD",
      action: "starts-with" },

    "ends-with": {
      label: "workportal-widget-filter-operators-ends-with",
      defaultLabel: "Ends with",
      appliesTo: ["Text"],
      operator: "WILDCARD",
      action: "ends-with" },

    "greater-equals-than": {
      label: "workportal-widget-filter-operators-greater-or-equal-than",
      appliesTo: ["Number", "DateTime", "Currency"],
      operator: "FROM",
      action: "from" },

    "less-equals-than": {
      label: "workportal-widget-filter-operators-less-or-equal-than",
      appliesTo: ["Number", "DateTime", "Currency"],
      operator: "TO",
      action: "to" },

    "between": {
      label: "workportal-widget-filter-operators-between",
      defaultLabel: "Between",
      appliesTo: ["Number", "DateTime", "Currency"],
      operator: "BETWEEN",
      action: "between" },

    "is-null": {
      label: "workportal-widget-filter-operators-is-empty",
      defaultLabel: "Is empty",
      appliesTo: ["Text", "Number", "DateTime", "Currency"],
      operator: "EXACT",
      action: "is-null" },

    "wildcard": {
      label: "Wildcard",
      appliesTo: ["Text"],
      operator: "WILDCARD",
      action: "wildcard",
      skip: true },

    "default": {
      label: "Default",
      appliesTo: ["Number", "Currency"],
      operator: "EXACT",
      action: "default",
      skip: true } },



  // region // #region CONTROL BASE

  /**
   * Constructor
   * @param data
   */
  initializeData: function initializeData(data) {

    // Call base
    this._super(data);

    var properties = this.properties;

    properties.filterType = properties.filterType || "";
    properties.renderType = this.Class.DATATYPE;
    properties.data = this.getOptions();
    properties.orientation = properties.orientation || "";

    this.currentFilter = {};
    this.controlInitial = null;
    this.controlEnd = null;

  },

  /**
      * Method to customize each render after processed
      */
  postRender: function postRender() {
    var $control = this.getControl();
    this.queryFilter = $(".bz-query-filter", $control);

    // Call base
    this._super();
  },

  /**
      * Method to customize each control
      * @returns {jQuery}
      */
  renderControl: function renderControl() {
    var template = this.renderFactory.getTemplate("query.advanced");
    var html = $.fasttmpl(template, {});

    return html;
  },

  processComplexValue: function processComplexValue(value) {
    var newValue = value;
    var isString = newValue && $.trim(newValue).length > 0;
    var isArray = isString && /^\[.*?\]$/i.test(newValue);

    if (isString && isArray) {
      newValue = JSON.parse(newValue);
    } else if (isString) {
      newValue = [newValue];
    } else {
      newValue = newValue || [];
    }

    return newValue;
  },

  /**
      * Method to render non editable values
      */
  renderReadOnly: function renderReadOnly() {
    var result;

    if (this.isCurrentDefaultFilter()) {
      result = this._super();
    } else {
      result = this.renderReadOnlyFilter();
    }

    return result;
  },

  renderReadOnlyFilter: function renderReadOnlyFilter() {
    var template = this.renderFactory.getTemplate("query.advanced.readonly");
    var filter = this.getCurrentFilter();
    var value = this.getValue() == "null" || bizagi.util.isNull(this.getValue()) ? "" : this.getValue();
    var $templateFilterReadonly = $.tmpl(template, {
      filterName: this.getResourceByKey(filter.label, filter.defaultLabel) });


    var $filterValueContainer = $(".bz-query-advanced__filter", $templateFilterReadonly);

    if (this.isCurrentBetweenFilter()) {
      this.renderReadOnlyBetween($filterValueContainer);
    } else if (this.isCurrentTagFilter()) {
      this.renderReadOnlyTags($filterValueContainer);
    } else {
      $filterValueContainer.append("<li class='bz-query-advanced__filter-input'><span>" + value + "</span></li>");
    }

    return $templateFilterReadonly;
  },

  renderReadOnlyBetween: function renderReadOnlyBetween(container) {
    var values = this.processComplexValue(this.getValue());
    if (values.length === 0) return;

    var valueFrom = values[0] == "null" || bizagi.util.isNull(values[0]) ? "" : values[0];
    var valueTo = values[1] == "null" || bizagi.util.isNull(values[1]) ? "" : values[1];
    var from = "<li class='bz-query-advanced__filter-input-from'><span>" + valueFrom + "</span></li>";
    var to = "<li class='bz-query-advanced__filter-input-to'><span>" + valueTo + "</span></li>";

    container.append(from).append(to);
  },

  renderReadOnlyTags: function renderReadOnlyTags(container) {
    var values = this.processComplexValue(this.getValue());
    if (values.length === 0) return;

    var template = this.renderFactory.getTemplate("query.advanced-pill");

    // Render tag items
    values.forEach(function (value) {
      var $ChildToAdd = $.tmpl(template, {
        id: this.getTagId(),
        value: this.formatTagItem(value || ""),
        editable: false });


      // Append tag
      container.append($ChildToAdd);
    });

  },

  /**
      * The render's behaviour to add handlers
      */
  configureHandlers: function configureHandlers() {
    // Call to base
    this._super();

    // Call to filters
    this.configureFilterHandlers();

    // Call to tag handlers
    this.configureTagHandlers();

    // Call to default handlers, Not apply to wildcard filter
    if (!/Text/i.test(this.properties.renderType))
    this.configureDefaultHandlers();
  },

  /**
      * Add handlers to query-filter control
      */
  configureFilterHandlers: function configureFilterHandlers() {
    var self = this;
    var $control = self.getControl();
    var $filterContainer = $(".bz-query-advanced__tags", $control);

    $filterContainer.on("click", function (evt) {
      var $context = $(evt.target);
      var isModeWildcard = $control.find(".bz-query-advanced").data("mode-wildcard") || false;

      var filterTagExist = self.filterOptionIsShowed();
      var clickOnFilter = $context.hasClass("bz-query-advanced__filter") ||
      $context.hasClass("bz-query-advanced__tag") ||
      $context.hasClass("bz-query-advanced__tag-span") ||
      $context.hasClass("bz-btn-icn bz-query-advanced__tag-arrow");

      if (!isModeWildcard && (filterTagExist && clickOnFilter || !filterTagExist)) {
        self.onToggleDataView();
      }

    });

    $control.on("click", ".bz-query-advanced__clean", function () {
      self.onToggleDataOptions();
    });

  },

  /**
      * Add handlers to query-filter control
      */
  configureTagHandlers: function configureTagHandlers() {
    var self = this;
    var $control = this.getControl();

    // Add pill on enter and tab
    $control.on("keydown", "input[data-action='tags']", function (evt) {
      var key = evt.key || evt.charCode || evt.keyCode;
      var valueTag = null;

      if (key === "Tab" || key === "Enter") {
        var $tagInput = $(this);
        valueTag = self.getValidTagValue($tagInput.val());

        if (valueTag) {
          self.addTag({ id: self.getTagId(), value: valueTag, displayValue: valueTag });
          $tagInput.val("");
          evt.preventDefault();
        }
      } else if (key === "Backspace") {
        valueTag = $(this).val();

        if (!valueTag) {
          self.removeLastTag();
        }
      }
    });

    // Add pill on focus out
    $control.on("blur", "input[data-action='tags']", function () {
      var $tagInput = $(this);
      var valueTag = self.getValidTagValue($tagInput.val());

      if (valueTag) {
        self.addTag({ id: self.getTagId(), value: valueTag, displayValue: valueTag });
        $tagInput.val("");
      }
    });

    // Remove pills on click
    $control.on("click", ".bz-query-advanced__remove-pill-icon", function (evt) {
      var tagId = Number($(evt.target).attr("data-id") || 0);
      self.removeTag(tagId);
    });

  },

  /**
      * Add handlers to query-filter control
      */
  configureDefaultHandlers: function configureDefaultHandlers() {
    var self = this;
    var $control = self.getControl();
    var $buttonClean = $(".bz-query-advanced__clean", $control);

    $control.on("change", ".bz-query-advanced__input", function () {
      if (!self.filterOptionIsShowed()) {
        if ($.trim(this.value).length > 0) {
          self.setCurrentFilter({ id: "default" });
          $buttonClean.removeClass('bz-hidden');

          // Hide list of filters
          self.closeFloatView();
        } else {
          self.setCurrentFilter({ id: "" });
          $buttonClean.addClass('bz-hidden');
        }
      }
    });
  },

  /**
      * Public method to determine if a value is valid or not
      * @param invalidElements
      * @returns {*}
      */
  isValid: function isValid(invalidElements) {
    // For base filter
    if (!this._super(invalidElements)) return false;

    // For single filter
    if (!!this.controlInitial && !this.controlInitial.isValid(invalidElements)) return false;

    // For between filter
    if (!!this.controlEnd && !this.controlEnd.isValid(invalidElements)) return false;

    return true;
  },

  /**
      * Check if a render has no value for required validation
      * @returns {boolean|*}
      */
  hasValue: function hasValue() {
    if (this.isCurrentNullFilter()) return true;
    return this._super();
  },

  // endregion // #endregion

  // region // #region METHODS TO LIST FILTERS

  /**
   * Toggle of data view
   */
  onToggleDataView: function onToggleDataView() {
    this.floatViewIsCollapsed() ? this.closeFloatView() : this.renderFloatView();
  },

  /**
      * Check if float list is collapsed
      * @returns {boolean}
      */
  floatViewIsCollapsed: function floatViewIsCollapsed() {
    var $listView = $(".bz-query-advanced__content", this.element);

    return $listView && $listView.length > 0;
  },

  /**
      * Get data and render the float list view
      */
  renderFloatView: function renderFloatView() {
    var self = this;
    var template = self.renderFactory.getTemplate("query.advanced-item");

    var filters = self.properties.data;
    var $floatList = self.showFloatListView(template, { datasource: filters }, {});

    self.floatListHandlers($floatList);
  },

  /**
      * Show float list view
      * @param template
      * @param data
      */
  showFloatListView: function showFloatListView(template, data) {
    var self = this;
    var $control = self.getControl();
    var $containerControl = $control.closest(".ui-bizagi-control-wrapper");
    var $containerRender = $containerControl.closest(".bz-form-old-item");
    var $floatList = $.tmpl(template, data);
    var widthControl = self.queryFilter.width() - 2;

    $containerRender.addClass("bz-query-advanced__visible");
    $containerControl.addClass("bz-query-advanced__visible bz-query-advanced__floats");
    $containerControl.append($floatList);

    $floatList.width(widthControl);

    $floatList.position({
      my: "left top",
      at: "left bottom",
      of: self.queryFilter,
      collision: "fit" }).
    hide();

    // Display view
    $floatList.show();

    // Stop bubbling outside the dropdown
    $floatList.on("mousedown", function (ev) {
      ev.preventDefault();
      return false;
    });

    // Add global handler to close the float view
    var timeoutMouseDown = setTimeout(function () {
      $(document).one("mousedown.query-advanced", function () {
        $floatList.trigger('closeView');
      });
      clearTimeout(timeoutMouseDown);
    }, 0);

    return $floatList;
  },

  /**
      * Update the float list view
      */
  closeFloatView: function closeFloatView() {
    var $control = this.getControl();
    var $containerControl = $control.closest(".ui-bizagi-control-wrapper");
    var $containerRender = $containerControl.closest(".bz-form-old-item");

    var $listView = $($containerRender).find(".bz-query-advanced__content");

    $listView.hide();

    this.destroyFloatView($listView);
  },

  /**
      * Destroy the float list view
      * @param $list
      */
  destroyFloatView: function destroyFloatView($list) {
    var $control = this.getControl();
    var $containerControl = $control.closest(".ui-bizagi-control-wrapper");
    var $containerRender = $containerControl.closest(".bz-form-old-item");

    if ($containerControl.hasClass("bz-query-advanced__visible")) {
      $containerControl.removeClass("bz-query-advanced__visible");
    }

    if ($containerControl.hasClass("bz-query-advanced__floats")) {
      $containerControl.removeClass("bz-query-advanced__floats");
    }

    if ($containerRender.hasClass("bz-query-advanced__visible")) {
      $containerRender.removeClass("bz-query-advanced__visible");
    }

    $list.remove();
  },

  floatListHandlers: function floatListHandlers($floatListContext) {
    var self = this;
    var $listContainer = $floatListContext.find(".bz-query-advanced__list li");

    $listContainer.click(function () {
      var $context = $(this);

      var id = $context.data("id");
      var element = $("span", $context)[0];
      var value = element.innerText;

      var newValue = { id: id, value: value };

      if (self.getCurrentFilter().id !== newValue.id) {
        if (!self.filterOptionIsShowed()) {
          self.showHideCleanButton(true);
        }

        self.destroyFilterContent();
        self.setSelectedFilter(newValue);
      } else {
        self.closeFloatView();
      }


      // Add focus to first control
      if (self.controlInitial) {
        $("input", self.controlInitial.getControl()).focus();
      }
    });


    $floatListContext.on("closeView", function () {
      self.closeFloatView();

      $(document).unbind("mousedown.query-advanced");
    });
  },

  setSelectedFilter: function setSelectedFilter(item) {
    this.onFilterChange(item);
    this.closeFloatView();
  },

  // endregion // #endregion

  // region // #region METHODS TO TAGS FILTER

  tagIsAdded: function tagIsAdded(newTagItem) {
    var values = (this.getDataValue() || []).
    map(function (tagItem) {
      return tagItem.value;
    });

    return values.includes(newTagItem.value);
  },

  addTag: function addTag(tagItem) {
    if (this.tagIsAdded(tagItem)) return;

    var form = this.getFormContainer();
    form.clearValidationMessages();

    if (this.isMaximumTagsValid()) {
      var $container = $(".bz-query-advanced__filter-input", this.getControl());
      var template = this.renderFactory.getTemplate("query.advanced-pill");
      var $templateItem = $.tmpl(template, {
        id: tagItem.id,
        value: tagItem.displayValue,
        editable: this.properties.editable });


      // Append tag
      $container.before($templateItem);

      // Update data
      this.addElement(tagItem);
    } else {
      var message = this.getResourceByKey("workportal-tags-error-limit-exceeded", "You cannot add more than #maxTags# criteria elements").replace("#maxTags#", this.MAX_TAGS_SUPPORTED);
      form.addErrorMessage(message);
    }
  },

  removeTag: function removeTag(tagId) {
    $("li[data-id=" + tagId + "]", this.getControl()).remove();
    // remove and update data
    this.removeElement({ id: tagId });
  },

  removeLastTag: function removeLastTag() {
    var tags = this.getDataValue();
    var lastTag = tags[tags.length - 1];
    // remove and update data
    if (lastTag) {
      this.removeTag(lastTag.id);
    }
  },

  /**
      * Get new id for tag filter
      * @returns {number}
      */
  getTagId: function getTagId() {
    return Math.ceil(Math.random() * 1000000);
  },

  /**
      * Add format to tag
      * @param value
      * @returns {*}
      */
  formatTagItem: function formatTagItem(value) {
    return $.trim(value);
  },

  /**
      * Add format to string
      */
  performTagItems: function performTagItems() {
    var resultString = "";
    var listString = (this.getDataValue() || []).
    filter(function (item) {
      return item && !!item.value;
    }).map(function (item) {
      return item.value;
    });

    if (listString.length > 1) {
      resultString = JSON.encode(listString);
    } else {
      resultString = listString && listString[0] || "";
    }

    return resultString;
  },

  /**
      * Check if maximum number of tag is valid
      * @returns {boolean}
      */
  isMaximumTagsValid: function isMaximumTagsValid() {
    var list = this.getDataValue() || [];

    return list && list.length < this.MAX_TAGS_SUPPORTED;
  },

  /**
      * Get list of data
      * @returns {*|Array}
      */
  getDataValue: function getDataValue() {
    var list;

    try {
      list = JSON.parse(this.value);

      return list || [];
    } catch (e) {
      return [];
    }
  },

  /**
      * Returns the internal value
      * @returns {*|Array}
      */
  getValue: function getValue() {
    if (this.isCurrentTagFilter()) return this.performTagItems();

    // Call to base
    return this._super();
  },

  /**
      * Return the value
      * @param {*} value
      */
  getValidTagValue: function getValidTagValue(value) {
    return $.trim(value);
  },

  /**
      * Add elements to global value
      * @param value
      */
  addElement: function addElement(value) {
    var newValue = this.getDataValue();

    newValue.push(value);

    this.setValue(newValue);
  },

  /**
      * Remove elements to global value
      * @param value
      */
  removeElement: function removeElement(value) {
    var idToRemove = value.id;
    var newValue = this.getDataValue().
    filter(function (item) {
      return item.id !== idToRemove;
    });

    this.setValue(newValue);
  },

  filterOptionIsShowed: function filterOptionIsShowed() {
    var $control = this.getControl();
    var $listTag = $(".bz-query-advanced__filter > .bz-query-advanced__tag", $control);

    return $listTag && $listTag.length > 0;
  },

  // endregion // #endregion

  // region // #region METHODS TO QUERY FORMS

  /**
   * Toggle the clean options
   */
  onToggleDataOptions: function onToggleDataOptions() {
    this.showHideCleanButton();

    this.cleanDataFilter(true);
  },

  /**
      * onFilterChange, destroy current content and add render new content
      * @param {*} item
      */
  onFilterChange: function onFilterChange(item) {
    // Update filter
    this.cleanDataFilter(false);
    this.setCurrentFilter(item);

    var $control = this.getControl();
    var action = this.getCurrentAction();

    // Remove data-action
    $(".bz-query-advanced__input", $control).removeAttr("data-action");
    $(".bz-query-advanced__tags", $control).removeAttr("data-action");

    if (action) {
      $(".bz-query-advanced__tags", $control).attr("data-action", action);

      this.defineLiteControl({ typeFilter: action });

      if (this.isCurrentNullFilter()) {
        this.setValue(this.getValue());
      }
    }
  },

  /**
      * Remove data of control
      * @param refresh
      */
  cleanDataFilter: function cleanDataFilter(refresh) {
    this.controlInitial = null;
    this.controlEnd = null;

    this.destroyFilterContent();
    this.setCurrentFilter({ id: "" });
    this.cleanData();

    $(".bz-query-advanced__tag", this.getControl()).remove();

    if (refresh) {
      this.defineLiteControl({ typeFilter: "" });
    }
  },

  /**
      * Get type of search
      * @returns {string}
      */
  getSearchType: function getSearchType() {
    var currentFilter = this.getCurrentFilter();
    return currentFilter && currentFilter.operator || "EXACT";
  },

  /**
      * Destroy content of filters
      */
  destroyFilterContent: function destroyFilterContent() {
    // Remove data-action
    $(".bz-query-advanced__filter", this.getControl()).empty();
  },

  /**
      * Perform query-value by search type
      * @param queryValue
      * @param searchType
      * @returns {*}
      */
  performQueryValue: function performQueryValue(queryValue, searchType) {
    var newQueryValue = queryValue;

    if (searchType === "starts-with") {
      newQueryValue = newQueryValue ? newQueryValue + "%" : "%";
    } else if (searchType === "ends-with") {
      newQueryValue = newQueryValue ? "%" + newQueryValue : "%";
    } else if (searchType === "is-null") {
      newQueryValue = "null";
    } else if (["equals", "not-equals"].includes(searchType)) {
      newQueryValue = this.performTagItems();
    }

    return newQueryValue;
  },

  formatInputValue: function formatInputValue(value) {
    return typeof value !== "undefined" && !bizagi.util.isEmpty(value) ? value : "null";
  },

  /**
      * Add selected elements
      */
  addSelectedElements: function addSelectedElements() {
    var self = this;
    var $control = this.getControl();
    var values = this.getDataValue() || [];
    var editable = this.properties.editable;

    var $tagsContainer = $(".bz-query-advanced__filter-input", $control);
    var template = this.renderFactory.getTemplate("query.advanced-pill");

    // Clean data
    $tagsContainer.remove(".bz-query-advanced__pill");

    // Render tag items
    values.forEach(function (tagItem) {

      var $ChildToAdd = $.tmpl(template, {
        id: tagItem.id,
        value: self.formatTagItem(tagItem.value || ""),
        editable: editable });


      // Append tag
      $tagsContainer.before($ChildToAdd);
    });

  },

  // endregion // #endregion

  // region // #region METHODS TO LITE CONTROLS

  /**
   * Return render properties
   * @param {*} params
   */
  getRenderProperties: function getRenderProperties(params) {
    var self = this;
    var form = self.getFormContainer();
    var properties = self.properties;

    params = params || {};

    var propertiesRender = {
      "id": properties.id,
      "xpath": properties.xpath + "_" + params.typeControl,
      "displayType": "value",
      "type": properties.type,
      "orientation": properties.orientation };


    var renderProperties = {
      data: {
        properties: $.extend({},
        propertiesRender,
        self.getRequiredProperties()) },

      mode: "execute",
      parent: form,
      renderFactory: self.renderFactory,
      resources: bizagi.localization,
      dataService: self.dataService };


    if (params.isRange) {

      renderProperties.data.properties.isRange = params.isRange || false;
      renderProperties.data.properties.isMinimumValue = params.isMinimum || false;
      renderProperties.data.properties.isMaximumValue = params.isMaximum || false;

      if (params.isMinimum) {
        renderProperties.data.properties.displayName = bizagi.localization.getResource("bz-rp-components-time-from-label");
      } else {
        renderProperties.data.properties.displayName = bizagi.localization.getResource("bz-rp-components-time-to-label");
      }
    }

    return renderProperties;
  },

  /**
      * Define the controls depend of filter type
      * @param params
      */
  defineLiteControl: function defineLiteControl(params) {
  },

  /**
      * Set the rendered controls
      * @param controlInitial
      * @param controlEnd
      */
  setLiteControls: function setLiteControls(controlInitial, controlEnd) {
  },

  /**
      * Render control
      * @param filterType
      * @param $canvas
      * @param control
      */
  renderLiteControl: function renderLiteControl(filterType, $canvas, control) {
  },

  /**
      * Get template by type of control
      * @param filterType
      */
  getTemplateByType: function getTemplateByType(filterType) {
  },

  getRequiredProperties: function getRequiredProperties() {
    return {};
  },

  // endregion // #endregion

  // region // #region UTILS TO FILTERS

  /**
   * Get Datasource by data type
   * @returns {}
   */
  getOptions: function getOptions() {
    var self = this;
    var operators = self.OPERATORS;
    var filterType = self.properties.renderType;
    var result = [];

    for (var item in operators) {
      var operator = operators[item];

      if ($.inArray(filterType, operator.appliesTo) >= 0 && !operator.skip) {
        result.push({
          value: self.getResourceByKey(operator.label, operator.defaultLabel),
          id: item });

      }
    }

    return result;
  },

  showHideCleanButton: function showHideCleanButton(show) {
    var $control = this.getControl();

    if (!!$control) {
      var $cleanButton = $(".bz-query-advanced__clean", $control);
      $cleanButton && show ? $cleanButton.removeClass('bz-hidden') : $cleanButton.addClass('bz-hidden');
    }
  },

  onToggleHideElement: function onToggleHideElement(elementClass) {
    var $control = this.getControl();

    if (!!$control) {
      var $buttonClose = $(elementClass, $control);

      $buttonClose && $buttonClose.toggleClass("bz-hidden");
    }
  },

  setCurrentFilter: function setCurrentFilter(filter) {
    var operators = this.OPERATORS;
    var idFilter = filter && filter.id ? filter.id : "";

    if (idFilter && operators[idFilter]) {
      this.currentFilter = Object.assign({}, operators[idFilter], { id: idFilter });
    } else {
      this.currentFilter = {};
    }

  },

  getCurrentFilter: function getCurrentFilter() {
    return this.currentFilter || {};
  },

  getCurrentAction: function getCurrentAction() {
    var filter = this.getCurrentFilter();
    var action = filter && filter.action;

    return action || "";
  },

  isCurrentTagFilter: function isCurrentTagFilter() {
    return this.isCurrentFilter("tags");
  },

  isCurrentBetweenFilter: function isCurrentBetweenFilter() {
    return this.isCurrentFilter("between");
  },

  isCurrentNullFilter: function isCurrentNullFilter() {
    return this.isCurrentFilter("is-null");
  },

  isCurrentWildcardFilter: function isCurrentWildcardFilter() {
    return this.isCurrentFilter("wildcard");
  },

  isCurrentContainsFilter: function isCurrentContainsFilter() {
    return this.isCurrentFilter("like");
  },

  isCurrentStartsWithFilter: function isCurrentStartsWithFilter() {
    return this.isCurrentFilter("starts-with");
  },

  isCurrentEqualsThanFilter: function isCurrentEqualsThanFilter() {
    return this.isCurrentFilter("less-equals-than") ||
    this.isCurrentFilter("greater-equals-than");
  },

  isCurrentEndsWithFilter: function isCurrentEndsWithFilter() {
    return this.isCurrentFilter("ends-with");
  },

  isCurrentFilter: function isCurrentFilter(filter) {
    return this.getCurrentAction() === filter;
  },

  isCurrentDefaultFilter: function isCurrentDefaultFilter() {
    var currentAction = this.getCurrentAction();
    return currentAction === this.defaultFilter ||
    currentAction === {} ||
    currentAction === "";
  },

  /**
      * This method returns the localized version of the resource
      * @param resource
      * @param fallback
      * @returns {string}
      */
  getResourceByKey: function getResourceByKey(resource, fallback) {
    fallback = fallback || resource;

    return bizagi.localization.getResource(resource, fallback);
  },

  prepareForSetValue: function prepareForSetValue() {
    var currentFilter = this.getCurrentFilter();
    var noFilterSettled = bizagi.util.isEmpty(currentFilter);

    if (noFilterSettled) {
      this.setFilter(this.defaultFilter);
    }
  },

  /**
      * Perform data value by filter type
      * @param value
      * @returns {*}
      */
  performDataValue: function performDataValue(value) {
    var self = this;
    var newValue = value;

    if (self.isCurrentTagFilter()) {
      var dataList = [];
      var isString = newValue && $.trim(newValue).length > 0;
      var isArray = isString && /^\[.*?\]$/i.test(newValue);

      if (isString && !isArray) {
        dataList = [newValue];
      } else if (isArray) {
        dataList = self.getDataValue() || [];
      }

      var list = (dataList || []).map(function (item) {
        return {
          id: self.getTagId(),
          value: item };

      });

      return list || "";
    } else if (newValue && $.trim(newValue).length > 0) {
      if (self.isCurrentStartsWithFilter()) {
        newValue = newValue.replace(/%$/i, "");
      } else if (self.isCurrentEndsWithFilter()) {
        newValue = newValue.replace(/^%/i, "");
      }
    }

    return newValue;
  },

  /**
      * Perform list value by filter type
      * @param value
      * @returns {*}
      */
  performListValue: function performListValue(value) {
    var action = this.getCurrentAction();
    var newValue = value;

    if (action === "between") {
      var dataList = [];
      var isString = newValue && $.trim(newValue).length > 0;
      var isArray = isString && /^\[.*?\]$/i.test(newValue);

      if (isString && isArray) {
        dataList = JSON.parse(value);

        newValue = {
          from: dataList[0] && dataList[0] !== "null" ? dataList[0] : "",
          to: dataList[1] && dataList[1] !== "null" ? dataList[1] : "" };

      } else {
        //if value is not an array
        newValue = {
          from: value,
          to: value };

      }
    }

    return newValue;
  },

  /**
      * Update current filter type
      * @param filterType
      */
  setFilter: function setFilter(filterType) {
    var action = "";
    var noFilterSettled = bizagi.util.isEmpty(this.getCurrentFilter());

    if (!!filterType) {
      this.setCurrentFilter({ id: filterType });
      action = this.getCurrentAction();
    }

    !noFilterSettled && this.showHideCleanButton(true);

    this.defineLiteControl({ typeFilter: action });
  },

  // endregion // #endregion

  /**
   * Add the render data to the given collection in order to send data to the server
   * @param renderValues
   */
  collectDataQueryForm: function collectDataQueryForm(renderValues) {
    var self = this;
    var properties = self.properties;
    var xpath = properties.xpath;
    var value = self.getValue();
    var queryValue = null;

    if (!bizagi.util.isEmpty(xpath)) {
      var searchType = self.getSearchType();
      var filter = this.getCurrentFilter();
      var filterType = filter && filter.id || "";

      if (value !== null && typeof value !== "undefined") {
        queryValue = value !== "" ? value : null;
      }

      if (queryValue !== null || properties.included || filterType === "is-null") {
        var newQueryValue = self.performQueryValue(queryValue, filterType);

        var newParameter = {
          value: newQueryValue,
          included: self.properties.included,
          xpath: properties.xpath,
          searchType: searchType,
          orderType: "NONE",
          filterType: filterType,
          advancedQuery: properties.advancedQuery || false,
          renderId: self.getRenderId() };


        if (typeof properties.displayXpath !== "undefined" && properties.displayXpath !== "") {
          newParameter.displayXpath = properties.displayXpath;
        }

        renderValues.push(newParameter);
      }
    }
  } });